import { Component, OnDestroy, OnInit } from '@angular/core';
import {
  AbstractControl,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { BehaviorSubject, Subject, Subscription } from 'rxjs';
import { filter, startWith, takeUntil } from 'rxjs/operators';
import {
  AccountInformation,
  Address,
  BeneficiaryContactItem,
  ContactItem,
  BeneficiaryList,
  CounterPartyFields,
  FetchCreditorItemsParams,
  getAccountHolderAddress,
  getContactBankAddress,
  HiddenFields,
  isAddressTruthy,
  InitiatorFields,
  InternalAccountFormItem,
  PaymentBaseFields,
  PaymentFormField,
  PaymentFormFieldConfig,
  PaymentFormFieldHooks,
  PaymentFormFieldOptions,
  sameAccountsValidator,
} from '@backbase/payment-orders-ang';
import { DeprecationsService } from '@backbase/foundation-ang/future';

import {
  registerFormControl,
  registerFormGroup,
} from '../../helpers/form-helpers';
import { VisibilityService } from '../../services/visibility.service';
import { triggerHook } from '../../helpers/hooks-helpers';
import { toggleBeneficiaryControls } from '../../helpers/generic-utils';

@Component({
  selector: 'bb-payord-beneficiary-selector',
  templateUrl: 'payord-beneficiary-selector.component.html',
})
export class PayordBeneficiarySelectorComponent
  implements PaymentFormField, OnInit, OnDestroy
{
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  options!: PaymentFormFieldOptions;

  selectedAccount!: InternalAccountFormItem;
  showBeneficiaryDetails = false;
  accountNameControl!: AbstractControl;
  beneficiaryAccountNameControl!: AbstractControl;
  accountNumberControl!: AbstractControl;
  bankCodeControl!: AbstractControl;
  bankNameControl!: AbstractControl;
  bankBicControl!: AbstractControl;
  beneficiaryAddressGroup!: AbstractControl;
  beneficiaryBankAddressGroup!: AbstractControl;
  beneficiaryBankGroup!: AbstractControl;
  saveContactControlName = CounterPartyFields.saveNewContact;
  saveContactControl!: AbstractControl;
  saveNewContactControl!: AbstractControl;
  selectedContactControl!: AbstractControl;
  manualEdit$ = new BehaviorSubject<boolean>(false);
  manualEditControl!: AbstractControl;
  isClosedPaymentControl!: AbstractControl;
  selectedBeneficiaryControl!: AbstractControl;
  enableBeneficiaryControl!: AbstractControl;
  // This control is deprecated, please use `initiatorIdControl` instead
  initiatorNameControl!: AbstractControl;
  initiatorIdControl!: AbstractControl;
  hideBeneficiaryFieldsControl!: AbstractControl;
  private arrangementIdControl!: AbstractControl;

  private query = '';
  private initiatorControlSubscription?: Subscription;
  private destroy$ = new Subject();

  constructor(
    private readonly visibilityService: VisibilityService,
    private readonly deprecationService: DeprecationsService
  ) {}

  ngOnInit() {
    this.setupFormFields();
    if (this.options.isWizardMode) {
      this.setupWizardFormFields();
    }
    triggerHook(PaymentFormFieldHooks.onInit, this);
    if (this.accountNameControl.value || this.options?.isTemplateMode) {
      this.showDependantFields();
    } else {
      this.hideDependantFields();
    }

    const initiatorControl = this.group.parent?.get(
      PaymentBaseFields.initiator
    );
    initiatorControl?.valueChanges
      .pipe(takeUntil(this.destroy$), startWith(initiatorControl.value))
      .subscribe(({ id }) => {
        this.accountNameControl.updateValueAndValidity();
      });

    this.arrangementIdControl?.valueChanges
      .pipe(takeUntil(this.destroy$))
      .subscribe((controlId) => {
        if (!this.isClosedPaymentControl.value) {
          toggleBeneficiaryControls(this.group, !!controlId);
        }
      });

    this.hideBeneficiaryFieldsControl?.valueChanges
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.onSearch('');
        this.enableBeneficiaryControl.setValue(true);
        this.manualEdit$.next(false);
        this.saveNewContactControl.setValue(false);
        if (!this.options.isWizardMode) {
          this.hideDependantFields();
        }
      });
  }

  ngOnDestroy() {
    this.initiatorControlSubscription?.unsubscribe();
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }

  setupFormFields() {
    this.deprecationService.logDeprecatedFeature(`
      "initiatorNameControl" is deprecated and will be removed in next major release, please use "initiatorIdControl" instead.
    `);
    this.accountNameControl = registerFormControl(
      this.group,
      this.config.name,
      [Validators.required, sameAccountsValidator()]
    );
    this.manualEditControl = registerFormControl(
      this.group.parent as FormGroup,
      HiddenFields.isManualEdit
    );
    this.isClosedPaymentControl = registerFormControl(
      this.group.parent as FormGroup,
      HiddenFields.isClosedPayment
    );
    this.enableBeneficiaryControl = registerFormControl(
      this.group.parent as FormGroup,
      HiddenFields.enableBeneficiary,
      [],
      [],
      true
    );
    this.beneficiaryAccountNameControl = registerFormControl(
      this.group,
      HiddenFields.beneficiaryAccountName
    );
    this.saveContactControl = registerFormControl(
      this.group,
      this.saveContactControlName
    );
    this.saveNewContactControl = registerFormControl(
      this.group,
      HiddenFields.showSaveNewContact
    );
    this.hideBeneficiaryFieldsControl = registerFormControl(
      this.group,
      HiddenFields.hideBeneficiaryFields
    );
    this.arrangementIdControl = registerFormControl(
      this.group,
      CounterPartyFields.id
    );
    this.selectedBeneficiaryControl = registerFormControl(
      <FormGroup>this.group.parent,
      HiddenFields.selectedBeneficiary
    );
    this.selectedContactControl = <FormGroup>(
      registerFormGroup(this.group, CounterPartyFields.selectedContact, {
        contactId: [''],
        accountId: [''],
      })
    );
    this.initiatorIdControl = <FormControl>(
      this.group.parent?.get([PaymentBaseFields.initiator, InitiatorFields.id])
    );
  }

  setupWizardFormFields() {
    this.accountNumberControl =
      this.group.controls[CounterPartyFields.accountNumber] ||
      this.registerAccountNumberControl();
    this.beneficiaryAddressGroup =
      this.group.controls[CounterPartyFields.postalAddress] ||
      this.registerAddressGroup(this.group);
    this.beneficiaryBankGroup =
      this.group.controls[CounterPartyFields.creditorBank] ||
      this.registerCreditorBankGroup();
    this.bankCodeControl =
      this.group.controls[
        `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankBranchCode}`
      ] || this.registerBankCodeControl();
    this.bankNameControl =
      this.group.controls[
        `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankName}`
      ] || this.registerBankNameControl();
    this.bankBicControl =
      this.group.controls[
        `${CounterPartyFields.creditorBank}.${CounterPartyFields.bic}`
      ] || this.registerBankBicControl();
    this.beneficiaryBankAddressGroup =
      this.group.controls[
        `${CounterPartyFields.creditorBank}.${CounterPartyFields.postalAddress}`
      ] || this.registerAddressGroup(<FormGroup>this.beneficiaryBankGroup);
    registerFormControl(<FormGroup>this.group, CounterPartyFields.schemeName);
  }

  registerAccountNumberControl() {
    return registerFormControl(
      <FormGroup>this.group,
      CounterPartyFields.accountNumber
    );
  }

  registerBankNameControl() {
    return registerFormControl(
      <FormGroup>this.beneficiaryBankGroup,
      CounterPartyFields.bankName
    );
  }

  registerBankBicControl() {
    return registerFormControl(
      <FormGroup>this.beneficiaryBankGroup,
      CounterPartyFields.bic
    );
  }

  registerBankCodeControl() {
    return registerFormControl(
      <FormGroup>this.beneficiaryBankGroup,
      CounterPartyFields.bankBranchCode
    );
  }

  registerAddressGroup(group: FormGroup) {
    const initialAddress = {
      addressLine1: [''],
      addressLine2: [''],
      streetName: [''],
      postCode: [''],
      town: [''],
      countrySubDivision: [''],
      country: [''],
    };
    return <FormGroup>(
      registerFormGroup(group, CounterPartyFields.postalAddress, initialAddress)
    );
  }

  registerCreditorBankGroup() {
    const creditorBankGroup = new FormGroup({});
    this.group.addControl(CounterPartyFields.creditorBank, creditorBankGroup);
    return creditorBankGroup;
  }

  registerCreditorBankAddress() {
    const creditorBankGroup = new FormGroup({});
    this.group.addControl(CounterPartyFields.creditorBank, creditorBankGroup);
    return creditorBankGroup;
  }

  private fetchCreditorItems(params: FetchCreditorItemsParams) {
    if (this.config.options?.fetchItems$) {
      this.config.options.fetchItems$.next(params);
    }
  }

  onLoadMoreAccounts(page: number) {
    this.deprecationService.logDeprecatedFeature(`
      "onLoadMoreAccounts" is deprecated since 3.9.3 and will be removed in 3.10.0, please use "onFetchMoreItems" instead.
    `);
    this.fetchCreditorItems({
      params: { from: page, query: this.query },
      type: BeneficiaryList.ACCOUNTS,
    });
  }

  onLoadMoreContacts(page: number) {
    this.deprecationService.logDeprecatedFeature(`
      "onLoadMoreContacts" is deprecated since 3.9.3 and will be removed in 3.10.0, please use "onFetchMoreItems" instead.
    `);
    this.fetchCreditorItems({
      params: { from: page, query: this.query },
      type: BeneficiaryList.CONTACTS,
    });
  }

  onFetchMoreItems(event: { page: number; type: BeneficiaryList }) {
    this.fetchCreditorItems({
      params: { from: event.page, query: this.query },
      type: event.type,
    });
  }

  onSearch(query: string) {
    this.query = query;
    this.manualEditControl.setValue(true);
    this.fetchCreditorItems({ params: { query } });
  }

  onSelectAccount(selectedItem: InternalAccountFormItem) {
    const accountNumberControl =
      this.group.controls[CounterPartyFields.accountNumber];
    const bankNameControl = this.group.get(
      `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankName}`
    );
    const bankCodeControl = this.group.get(
      `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankBranchCode}`
    );
    const beneficiaryAddressGroup = this.group.get(
      CounterPartyFields.postalAddress
    );
    const creditorBankBic = this.group.get(
      `${CounterPartyFields.creditorBank}.${CounterPartyFields.bic}`
    );
    this.setValue(this.accountNameControl, selectedItem.name || '');
    this.setValue(accountNumberControl, selectedItem.accountNumber || '');
    this.setValue(bankCodeControl, selectedItem.bankBranchCode);
    this.setValue(creditorBankBic, selectedItem.BIC || '');
    this.setValue(bankNameControl, '');
    this.setValue(this.arrangementIdControl, selectedItem.id);
    this.saveNewContactControl.setValue(false);
    this.setAddress(<FormGroup>beneficiaryAddressGroup, {});
    this.setDefaultCountry();
    this.showBeneficiaryDetails = true;
    this.enableBeneficiaryControl.setValue(false);
    this.manualEdit$.next(false);
    this.manualEditControl.setValue(false);
    this.selectedBeneficiaryControl.setValue(selectedItem);
    this.selectedContactControl.reset();
    this.saveContactControl.reset();
    this.showDependantFields();
    this.initiatorIdControl?.updateValueAndValidity();
  }

  onSelectContact(selectedcontact: BeneficiaryContactItem) {
    const accountNumberControl =
      this.group.controls[CounterPartyFields.accountNumber];
    const bankNameControl = this.group.get(
      `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankName}`
    );
    const bankCodeControl = this.group.get(
      `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankBranchCode}`
    );
    const creditorBankBic = this.group.get(
      `${CounterPartyFields.creditorBank}.${CounterPartyFields.bic}`
    );
    const beneficiaryAddressGroup = this.group.get(
      CounterPartyFields.postalAddress
    );
    const beneficiaryBankAddressGroup = this.group.get(
      `${CounterPartyFields.creditorBank}.${CounterPartyFields.postalAddress}`
    );
    const account = selectedcontact.accounts[0];
    const accountHolderAddress = this.getAccountHolderAddress(account);
    const beneficiaryAddress = isAddressTruthy(accountHolderAddress)
      ? accountHolderAddress
      : this.getBeneficiaryAddress(selectedcontact);
    const bankAddress = this.getContactBankAddress(account);
    this.setValue(this.accountNameControl, selectedcontact.name);
    this.setValue(
      this.beneficiaryAccountNameControl,
      selectedcontact.accountName || ''
    );
    this.setValue(
      accountNumberControl,
      account.accountNumber || account.IBAN || ''
    );
    this.setValue(bankCodeControl, account.bankCode || '');
    this.setValue(bankNameControl, account.bankName || '');
    this.setValue(creditorBankBic, account.BIC || '');
    this.setValue(this.arrangementIdControl, '');
    this.setValue(
      this.selectedContactControl,
      this.getSelectedContactDetails(selectedcontact)
    );
    this.selectedBeneficiaryControl.setValue(selectedcontact);
    this.setAddress(<FormGroup>beneficiaryAddressGroup, beneficiaryAddress);
    this.setAddress(<FormGroup>beneficiaryBankAddressGroup, bankAddress, true);

    this.showBeneficiaryDetails = true;
    this.manualEdit$.next(false);
    this.manualEditControl.setValue(false);
    this.enableBeneficiaryControl.setValue(false);
    this.saveContactControl.reset();
    this.showDependantFields();
    this.initiatorIdControl?.updateValueAndValidity();
  }

  private getSelectedContactDetails(selectedcontact: ContactItem) {
    // currently there is no id associated with a contact account. Will need populate accountId when contacts fix this.
    return { contactId: selectedcontact?.id, accountId: '' };
  }

  setAddressDetails(addressGroup: FormGroup, address: Address) {
    if (addressGroup) {
      if (isAddressTruthy(address)) {
        this.setAddress(addressGroup, address);
      }
    }
  }

  onManualEdit(search: string) {
    const bankaddressGroup = this.group.get(
      `${CounterPartyFields.creditorBank}.${CounterPartyFields.postalAddress}`
    );
    const banksAddressControls = this.options.isWizardMode
      ? this.getAddressFields(<FormGroup>bankaddressGroup)
      : [bankaddressGroup];
    this.accountNameControl.patchValue(search);
    this.manualEdit$.next(true);
    this.manualEditControl.setValue(true);
    this.saveNewContactControl.setValue(true);
    this.setValue(this.arrangementIdControl, '');
    this.selectedBeneficiaryControl.reset();
    this.resetControls([
      this.group.controls[CounterPartyFields.accountNumber],
      this.group.get(
        `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankName}`
      ),
      this.group.get(
        `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankBranchCode}`
      ),
      this.group.get(
        `${CounterPartyFields.creditorBank}.${CounterPartyFields.bic}`
      ),
      this.group.get(CounterPartyFields.postalAddress),
      this.selectedContactControl,
      ...banksAddressControls,
    ]);
    this.setDefaultCountry();
    this.visibilityService.showItems(this.config.parent);
    this.showDependantFields();
  }

  private setDefaultCountry() {
    const defaultCountry = this.options.isWizardMode
      ? {
          country: this.group.parent?.get(HiddenFields.defaultCountry)?.value,
        }
      : null;
    const beneficiaryBankAddressGroup = this.group.get(
      `${CounterPartyFields.creditorBank}.${CounterPartyFields.postalAddress}`
    );
    this.setAddress(
      <FormGroup>beneficiaryBankAddressGroup,
      { ...defaultCountry },
      true
    );
  }

  onSearchBlur(value: string) {
    this.accountNameControl.patchValue(value);
    if (!this.accountNameControl.touched) {
      this.accountNameControl.markAsTouched();
    }
  }

  getAddressFields(bankaddressGroup: FormGroup | null): Array<AbstractControl> {
    const addressControls: Array<AbstractControl> = [];
    if (bankaddressGroup) {
      for (const controlName in bankaddressGroup.controls) {
        if (bankaddressGroup.controls.hasOwnProperty(controlName)) {
          const control = bankaddressGroup.controls[controlName];
          if (controlName !== CounterPartyFields.country) {
            addressControls.push(control);
          }
        }
      }
    }
    return addressControls;
  }

  private resetControls(controls: Array<AbstractControl | null>) {
    controls.forEach((control) => {
      if (control) {
        control.reset();
      }
    });
  }

  showDependantFields() {
    if (this.config.options?.dependants) {
      this.visibilityService.showItems(
        this.config.parent,
        this.config.options.dependants
      );
    }
  }

  hideDependantFields() {
    if (this.config.options?.dependants) {
      this.visibilityService.hideItems(
        this.config.parent,
        this.config.options.dependants
      );
    }
  }

  getBeneficiaryAddress(contact: Address) {
    return {
      addressLine1: contact.addressLine1,
      addressLine2: contact.addressLine2,
      streetName: contact.streetName,
      town: contact.town,
      postCode: contact.postCode,
      countrySubDivision: contact.countrySubDivision,
      country: contact.country,
    };
  }

  getAccountHolderAddress(account: AccountInformation) {
    return getAccountHolderAddress(account);
  }

  getContactBankAddress(account: AccountInformation) {
    return getContactBankAddress(account);
  }

  setAddress(
    addressGroup: FormGroup | null,
    addressInfo: Address,
    isBankAddress = false
  ) {
    if (addressGroup) {
      if (isAddressTruthy(addressInfo) || isBankAddress) {
        addressGroup.patchValue({
          addressLine1: addressInfo.addressLine1,
          addressLine2: addressInfo.addressLine2,
          streetName: addressInfo.streetName,
          town: addressInfo.town,
          postCode: addressInfo.postCode,
          countrySubDivision: addressInfo.countrySubDivision,
          country: addressInfo.country,
        });
      } else {
        for (const controlName in addressGroup.controls) {
          if (
            !(
              isBankAddress &&
              this.options.retainBankCountry &&
              controlName === 'country'
            )
          ) {
            addressGroup.controls[controlName].reset();
          }
        }
      }
    }
  }

  setValue(
    control: AbstractControl | null,
    value?: string | boolean | number | object
  ) {
    if (control) {
      if (value) {
        control.patchValue(value);
      } else {
        control.reset();
      }
    }
  }

  onClearValue(value: string) {
    this.setValue(this.beneficiaryAccountNameControl, value);
  }

  isBeneficiaryDisabled(): boolean {
    return (
      (this.isClosedPaymentControl.value &&
        !this.enableBeneficiaryControl.value) ||
      !!this.arrangementIdControl.value
    );
  }
}
