import { Observable, BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';
import { DependantItem } from '@backbase/payment-orders-ang';

type ItemVisibility = Observable<{
  name?: string | undefined;
  items?: (string | DependantItem)[] | undefined;
}>;

@Injectable()
export class VisibilityService {
  private showSubject$ = new BehaviorSubject<{
    name?: string;
    items?: (string | DependantItem)[];
  }>({});
  private hideSubject$ = new BehaviorSubject<{
    name?: string;
    items?: (string | DependantItem)[];
  }>({});

  /**
   * Getter to show an item
   */
  get show$(): ItemVisibility {
    return this.showSubject$.asObservable();
  }

  /**
   * Getter to hide an item
   */
  get hide$(): ItemVisibility {
    return this.hideSubject$.asObservable();
  }

  /**
   * Shows the items
   *
   * @param [name] Item name
   * @param [items] Items to show
   * @returns
   */
  showItems(name?: string, items?: (string | DependantItem)[]) {
    this.showSubject$.next({ name, items });
  }

  /**
   * Hides the items
   *
   * @param [name] Item name
   * @param [items] Items to hide
   * @returns
   */
  hideItems(name?: string, items?: (string | DependantItem)[]) {
    this.hideSubject$.next({ name, items });
  }
}
