import { getLocaleNumberSymbol, NumberSymbol } from '@angular/common';
import { Component, OnInit, OnDestroy, Inject, LOCALE_ID } from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';
import { ProductSummaryItem } from '@backbase/data-ang/arrangements';
import {
  PaymentFormField,
  PaymentFormFieldConfig,
  PaymentFormFieldOptions,
  PaymentFormFieldHooks,
  PaymentBaseFields,
  ProductKinds,
  AmountOption,
  BaseObject,
  RemittanceInfoFields,
  InternalAccountFormItem,
} from '@backbase/payment-orders-ang';
import { StateService, PaymentState } from '@backbase/payment-orders-ang';
import BigNumber from 'bignumber.js';
import { Observable, Subscription } from 'rxjs';
import {
  pluck,
  tap,
  startWith,
  distinctUntilChanged,
  filter,
} from 'rxjs/operators';
import { registerFormControl } from '../../helpers/form-helpers';
import { mapLocalize } from '../../helpers/generic-utils';
import { triggerHook } from '../../helpers/hooks-helpers';
import { ActivatableFormField } from '../../models/activatable-form-field';
import { VisibilityService } from '../../services/visibility.service';

@Component({
  selector: 'bb-internal-amount-options',
  templateUrl: './payord-internal-amount-options.component.html',
})
export class PayordInternalAmountOptionsComponent
  extends ActivatableFormField
  implements OnInit, PaymentFormField, OnDestroy
{
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  toAccountProduct$!: Observable<InternalAccountFormItem> | undefined;
  toAccountDefaultRecord!: ProductSummaryItem | undefined;
  productKinds = ProductKinds;
  remittanceInfoFields = RemittanceInfoFields;
  amountOptions?: AmountOption[];
  customAmount!: string;
  touched = false;
  readonly id = `bb-compact-amount-${new Date().getTime()}`;
  private subscription$?: Subscription;

  /**
   * The format used for bignumber;
   */
  private readonly format: BigNumber.Format;
  private readonly groupSeparator: string;
  private readonly decimalSeparator: string;

  constructor(
    private readonly stateService: StateService,
    visibilityService: VisibilityService,
    @Inject(LOCALE_ID) private readonly locale: string
  ) {
    super(visibilityService);
    this.groupSeparator = getLocaleNumberSymbol(
      this.locale,
      NumberSymbol.CurrencyGroup
    );
    this.decimalSeparator = getLocaleNumberSymbol(
      this.locale,
      NumberSymbol.CurrencyDecimal
    );
    this.format = {
      groupSeparator: this.groupSeparator,
      decimalSeparator: this.decimalSeparator,
      groupSize: 3,
    };
  }
  ngOnInit() {
    this.registerFields();
    triggerHook(PaymentFormFieldHooks.onInit, this);
    this.control = this.group.controls[this.config.name];
    this.listenAmountOptionChanges();

    this.toAccountProduct$ = this.group.parent
      ?.get(PaymentBaseFields.counterparty)
      ?.valueChanges.pipe(
        startWith(
          this.group.parent?.get(PaymentBaseFields.counterparty)?.value
        ),
        pluck('accountData'),
        filter((account) => !!account),
        distinctUntilChanged((prev, curr) => prev.id === curr.id),
        tap((account: InternalAccountFormItem) => {
          const optionValue = this.control?.value;
          if (optionValue || optionValue === 0) {
            if (this.stateService.currentState !== PaymentState.EDIT) {
              this.control?.reset();
              this.group.controls[
                RemittanceInfoFields.amountOptionLabel
              ].reset();
              this.group.controls[
                RemittanceInfoFields.amountCurrencyGroup
              ].reset();
              this.group.controls['scheduleFrequency'].reset();
              this.group.controls['showFrequencySwitch'].reset();
            }
            this.customAmount = new BigNumber(
              this.group.controls[
                RemittanceInfoFields.amountCurrencyGroup
              ].value?.amount
            ).toFormat(this.format);
          }

          this.toggleDependants();
          this.toAccountDefaultRecord = account.defaultRecord;
          this.setAmountOptions(account);
          this.stateService.setPaymentState(PaymentState.INITIAL);

          if (this.options.noBalanceText) {
            if (this.isAmountOptionsZero(account) && this.control) {
              this.group.removeControl(this.config.name);
            } else if (!this.control) {
              registerFormControl(
                this.group,
                this.config.name,
                [Validators.required],
                this.options.asyncValidators
              );
              this.listenAmountOptionChanges();
            }
          }

          if (account.currency) {
            this.group
              .get(
                `${RemittanceInfoFields.amountCurrencyGroup}.${RemittanceInfoFields.currency}`
              )
              ?.patchValue(account.currency);
          }
        })
      );
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
    this.subscription$?.unsubscribe();
  }

  registerFields() {
    registerFormControl(this.group, RemittanceInfoFields.amountOptionLabel);
    registerFormControl(
      this.group,
      this.config.name,
      this.options.validators,
      this.options.asyncValidators
    );
    registerFormControl(this.group, 'scheduleFrequency');
    registerFormControl(this.group, 'showFrequencySwitch');
  }

  /**
   * Method to handle selecting amount option
   *
   * @param [index] index of selected amount option
   */
  amountOptionChange(index: number) {
    const option = this.amountOptions?.[index] || <AmountOption>{};
    const label = option.label;
    const currency = this.toAccountDefaultRecord?.currency;
    const amount = (this.toAccountDefaultRecord as BaseObject<any>)?.[
      option.amountApiField || ''
    ]?.toString();

    if (option.hasCustomAmountInput) {
      this.group.controls[RemittanceInfoFields.amountCurrencyGroup].reset({
        amount: '',
        currency,
      });
      this.group.controls[RemittanceInfoFields.amountOptionLabel].patchValue(
        label
      );
      this.group.controls['scheduleFrequency'].patchValue(
        option.scheduleFrequency
      );
      this.group.controls['showFrequencySwitch'].patchValue(
        option.showFrequencySwitch
      );
    }

    if (amount) {
      this.group.patchValue({
        [RemittanceInfoFields.amountCurrencyGroup]: { amount, currency },
        [RemittanceInfoFields.amountOptionLabel]: label,
        scheduleFrequency: option.scheduleFrequency,
        showFrequencySwitch: option.showFrequencySwitch,
      });
    }
    this.toggleDependants();
  }

  /**
   * Set amount options depend on selected product
   *
   * @param [product] selected account in counterParty product selector
   */
  setAmountOptions(product: InternalAccountFormItem): void {
    const { creditCard, loan } = this.options?.amountOptions || {};

    switch (product.productKindName) {
      case ProductKinds.creditCard:
        this.amountOptions = creditCard?.map(mapLocalize) || [];
        break;
      case ProductKinds.loanAccount:
        this.amountOptions = loan?.map(mapLocalize) || [];
        break;
      default:
        this.amountOptions = [];
    }
  }

  isAmountOptionsZero(account: InternalAccountFormItem) {
    if (!this.amountOptions?.length || !account) {
      return;
    }
    return this.amountOptions?.every((option: AmountOption) =>
      option?.amountApiField
        ? Number(
            (account.defaultRecord as BaseObject<any>)?.[option.amountApiField]
          ) === 0
        : true
    );
  }

  listenAmountOptionChanges() {
    this.subscription$ = this.control?.valueChanges
      .pipe(
        startWith(this.control?.value),
        distinctUntilChanged(),
        filter(
          (value) =>
            (value || value === 0) &&
            this.stateService.currentState !== PaymentState.EDIT
        )
      )
      .subscribe(this.amountOptionChange.bind(this));
  }
}
