import {
  Component,
  Input,
  OnInit,
  EventEmitter,
  OnDestroy,
} from '@angular/core';
import { VisibilityService } from '../../services/visibility.service';
import { takeUntil, map } from 'rxjs/operators';
import {
  AbstractControl,
  FormGroup,
  Validators,
  FormArray,
} from '@angular/forms';
import {
  PaymentFormGroup,
  Hideable,
  PaymentFormFieldConfig,
} from '@backbase/payment-orders-ang';
import { mapDependentItems } from '../../helpers/generic-utils';

@Component({
  selector: 'bb-payord-group',
  templateUrl: './payord-group.component.html',
})
export class PayordGroupComponent implements OnInit, OnDestroy {
  /**
   * The config of the form group
   */
  @Input() config!: PaymentFormGroup & Hideable;
  /**
   * The parent formgroup
   */
  @Input() group!: FormGroup;
  gc$ = new EventEmitter();
  eventName!: string;

  constructor(private readonly visibilityService: VisibilityService) {}

  ngOnInit() {
    this.eventName = this.config.parent
      ? `${this.config.parent}.${this.config.name}`
      : this.config.name;
    this.visibilityService.show$
      .pipe(
        map(({ name, items }) =>
          mapDependentItems(items, name).find(
            (dep) => dep.parent === this.eventName
          )
        ),
        takeUntil(this.gc$)
      )
      .subscribe((data) => {
        if (data?.items?.length) {
          this.showFields(data.items);
        }
      });

    this.visibilityService.hide$
      .pipe(
        map(({ name, items }) =>
          mapDependentItems(items, name).find(
            (dep) => dep.parent === this.eventName
          )
        ),
        takeUntil(this.gc$)
      )
      .subscribe((data) => {
        if (data?.items?.length) {
          this.hideFields(data.items);
        }
      });
  }

  scanHideableItems(items: string[]) {
    const hideableFields: { [key: string]: boolean } = {};
    items.forEach((itemName: string) => {
      hideableFields[itemName] = true;
    });
    return hideableFields;
  }

  showFields(fieldsNames: string[]) {
    const hideableFields = this.scanHideableItems(fieldsNames);
    let control: AbstractControl | null;
    this.config.fields.forEach(
      (field: (PaymentFormFieldConfig | PaymentFormGroup) & Hideable) => {
        if (hideableFields[field.name]) {
          field.hidden = false;
          control = this.group.get(`${this.config.name}.${field.name}`);
          if (control && field?.options?.isConditionalMandatory) {
            control.setValidators([
              Validators.required,
              ...(field.options.validators || []),
            ]);
            control.setAsyncValidators([
              ...(field.options.asyncValidators || []),
            ]);
            control.updateValueAndValidity();
          }
        }
      }
    );
  }

  hideFields(fieldsNames: string[]) {
    const hideableFields = this.scanHideableItems(fieldsNames);
    let control: AbstractControl | null;

    this.config.fields.forEach(
      (field: (PaymentFormFieldConfig | PaymentFormGroup) & Hideable) => {
        if (hideableFields[field.name]) {
          field.hidden = true;
          control = field?.options?.mapToField
            ? this.getMappedField(field.name, field.options.mapToField)
            : this.group.get(`${this.config.name}.${field.name}`);

          if (control && !field?.options?.keepValueOnHidden) {
            if (field?.options?.isConditionalMandatory) {
              control.setValidators(null);
              control.setAsyncValidators(null);
              control.updateValueAndValidity();
            }
            control.reset();
            if (field?.options?.defaultValue) {
              control.patchValue(field.options.defaultValue);
            }
          }
          this.hideNestedField(field);

          if ((<PaymentFormGroup>field)?.fields?.length) {
            (<PaymentFormGroup>field)?.fields?.forEach((nestedField) =>
              this.hideNestedField(nestedField)
            );
          }
        }
      }
    );
  }

  private hideNestedField(field: PaymentFormFieldConfig | PaymentFormGroup) {
    if (field?.options?.dependants && !field?.options?.keepDependantsOnHide) {
      mapDependentItems(field.options.dependants, field.parent).forEach(
        ({ parent, items }) => {
          this.visibilityService.hideItems(parent, items);
        }
      );
    }
  }

  private getMappedField(
    fieldName: string,
    mapToField: string
  ): AbstractControl | null {
    let parent: FormGroup | FormArray | null = this.group;
    let control: AbstractControl | null;

    while (parent) {
      control = parent.get(`${mapToField}.${fieldName}`);
      if (control) {
        return control;
      }
      parent = parent.parent;
    }
    return null;
  }

  ngOnDestroy(): void {
    this.gc$.next();
    this.gc$.complete();
  }
}
