import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnDestroy,
} from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup } from '@angular/forms';
import {
  PaymentFormGroup,
  PaymentFormItem,
  PaymentTypeConfig,
  StepperConfig,
  PaymentFormFieldConfig,
  MultipleFormGroup,
  MultiPaymentHooksConfig,
  MultiPaymentHooks,
  StepperAction,
  PaymentBaseFields,
  InitiatorFields,
  ManualBatchTypeConfig,
} from '@backbase/payment-orders-ang';
import { Subject, Observable } from 'rxjs';
import { debounceTime, distinctUntilChanged, takeUntil } from 'rxjs/operators';

import { triggerStepperHook } from '../../helpers/hooks-helpers';
import { ComponentsFactoryService } from '../../services/components-factory.service';
import { MultiPaymentService } from '../../services/multi-payment.service';
import { VisibilityService } from '../../services/visibility.service';
import { BatchOrderStateService } from '@backbase/payment-orders-ang';
import '@angular/localize/init';

@Component({
  selector: 'bb-multi-payment-form',
  templateUrl: './multi-payment-form.component.html',
  providers: [VisibilityService, ComponentsFactoryService],
})
export class MultiPaymentFormComponent implements OnInit, OnDestroy {
  /**
   * stepper Config for showing stepper details
   */
  @Input() stepperConfig!: StepperConfig[];
  /**
   * existing form
   */
  @Input() formItem?: FormGroup;
  /**
   * Manual batches configuration
   */
  @Input() config!: ManualBatchTypeConfig;
  /**
   * errorList to be displayed in the component.
   */
  @Input() errorList: Array<any> = [];
  /**
   * Event emitter when form is submitted
   */
  @Output() submitted = new EventEmitter();
  /**
   * Event Emitter when step is next.
   */
  @Output() stepNext = new EventEmitter();
  /**
   * Event Emitter when step is back.
   */
  @Output() back = new EventEmitter();
  /**
   * Event emitter when alert is hidden.
   */
  @Output() hideError = new EventEmitter<{ id: number; stepName: string }>();

  form!: FormGroup;

  controls: Array<MultipleFormGroup> = [];
  stepData: { [key: string]: any } = {};

  private destroy$ = new Subject<void>();

  totalTransfers$ = this.multiPaymentService.paymentTotalTransfers$;
  totalAmount$ = this.multiPaymentService.paymentTotalAmount$;
  currentTypeConfig!: PaymentTypeConfig;
  currentStepForm: Array<Observable<any> | undefined> = [];
  currentForm = {};
  subscription!: Observable<any>;
  formVal = new Subject();
  currency!: string;

  constructor(
    private fb: FormBuilder,
    private readonly componentsFactory: ComponentsFactoryService,
    private multiPaymentService: MultiPaymentService,
    public stateService: BatchOrderStateService
  ) {}

  ngOnInit(): void {
    this.form = this.formItem || this.fb.group({});

    if (this.config) {
      this.componentsFactory.registerCustomFields(this.config.customFields);
      this.createFormFields(this.config.fields, this.form, this.controls);
      this._listenFormChanges(this.form);
    }

    for (const step in this.stepperConfig) {
      if (this.stepperConfig.hasOwnProperty(step)) {
        this.stepData[this.stepperConfig[step].stepName] = {};
      }
    }
  }

  updateStepperDetails(currentStep: StepperConfig, action?: StepperAction) {
    switch (action) {
      case StepperAction.NEXT:
        this.stateService.resetErrors();
        if (this.isStepValid(currentStep.stepName)) {
          this.onStepSubmit();
        } else {
          this.stateService.addError(currentStep.stepName, {
            message: $localize`:@@bb-multi-payment-widget.form-error:Form error: Check entered data before proceeding to the next step.`,
          });
        }
        break;
      case StepperAction.BACK:
        this.stateService.resetErrors();
        this.back.emit();
        break;
      case StepperAction.SUBMIT:
        if (this.isStepValid(currentStep.stepName)) {
          this.stateService.resetErrors();
          this.onSubmit();
        }
        break;
    }
  }

  createFormFields(
    arr: Array<PaymentFormFieldConfig | PaymentFormGroup>,
    _group: FormGroup,
    controlsArr: Array<PaymentFormItem>,
    parentName?: string
  ) {
    arr.forEach((control: PaymentFormFieldConfig | PaymentFormGroup) => {
      const mappedField = control.options && control.options.mapToField;
      const group = mappedField
        ? <FormGroup>this.form.get(mappedField)
        : _group;
      const parent = mappedField || parentName;
      const subArray: Array<PaymentFormItem> = [];
      if (control.options?.fieldsConfig) {
        this.currency = control.options.fieldsConfig.amount.currency;
      }
      if ((<PaymentFormGroup>control).fields instanceof Array) {
        const subGroup =
          <FormGroup>group.get(control.name) || this.fb.group({});
        this.createFormFields(
          (<PaymentFormGroup>control).fields,
          subGroup,
          subArray,
          parent ? `${parent}.${control.name}` : control.name
        );
        group.addControl(control.name, subGroup);
        controlsArr.push({ ...control, fields: subArray, parent: parentName });
      } else {
        controlsArr.push({ ...control, parent });
      }
    });
  }

  isStepValid(currentStepName: string): boolean {
    const currentStepFields: Array<MultipleFormGroup> = this.controls.filter(
      (control) => control.stepName === currentStepName
    );
    this.currentStepForm = [];
    this.currentForm = {};

    const isValidArray: Array<boolean | undefined> = currentStepFields.reduce(
      (acc: Array<boolean | undefined>, steps: MultipleFormGroup) => {
        this.form.get(steps.name)?.markAllAsTouched();

        this.currentForm = {
          ...this.currentForm,
          ...this.form.get(steps.name)?.value,
        };
        acc.push(this.form.get(steps.name)?.valid);
        return acc;
      },
      []
    );
    return isValidArray.every(Boolean);
  }

  onSubmit() {
    this.form.markAsDirty();
    this.form.markAllAsTouched();

    if (this.form.valid) {
      this.submitted.emit(this.form);
    }
  }

  onStepSubmit() {
    if (
      JSON.stringify(
        this.stepData[this.stateService.currentStep?.stepName || '']
      ) !== JSON.stringify(this.currentForm)
    ) {
      this.stepData[this.stateService.currentStep?.stepName || ''] = this
        .stepData[this.stateService.currentStep?.stepName || '']
        ? this.currentForm
        : {};
    }
    this.stepNext.emit({
      form: this.currentForm,
      stepName: this.stateService.currentStep?.stepName,
    });
  }

  triggerButtonEvent(
    hooks: MultiPaymentHooksConfig | undefined,
    action: StepperAction,
    currentStep: StepperConfig
  ) {
    if (hooks) {
      triggerStepperHook(
        MultiPaymentHooks.onClick,
        hooks,
        this.updateStepperDetails.bind(this, currentStep, action),
        this.stateService.currentStep,
        action
      );
    } else {
      this.updateStepperDetails(currentStep, action);
    }
  }

  private _listenFormChanges(form: FormGroup) {
    const counterpartyControl = <FormArray>(
      form.get([PaymentBaseFields.counterparty])
    );
    counterpartyControl.valueChanges
      .pipe(distinctUntilChanged(), takeUntil(this.destroy$))
      .subscribe(() => {
        let amount = 0;
        let totalTransfers = 0;

        const formArray = form.get([
          PaymentBaseFields.counterparty,
          InitiatorFields.initiatorAccountGroup,
        ]);

        if (formArray) {
          const transferControls = (formArray as FormArray).controls;
          totalTransfers = transferControls.length;
          transferControls.forEach((control) => {
            if (control.get('amount')?.valid) {
              amount = +amount + +control.get('amount')?.value;
            }
          });
        }

        this.multiPaymentService.totalTransfers$.next(totalTransfers);
        this.multiPaymentService.totalAmount$.next(amount);
      });
    const initiatorControl = <FormControl>(
      form.get([PaymentBaseFields.initiator])
    );
    initiatorControl.valueChanges
      .pipe(debounceTime(500), distinctUntilChanged(), takeUntil(this.destroy$))
      .subscribe(() => {
        const formArray = form.get([
          PaymentBaseFields.counterparty,
          InitiatorFields.initiatorAccountGroup,
        ]);
        if (formArray) {
          (formArray as FormArray).controls.forEach((control) => {
            if (control.get('counterpartyName')?.touched) {
              control.get('counterpartyName')?.updateValueAndValidity();
              control.get('counterpartyName')?.markAllAsTouched();
            }
          });
        }
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  hideAlert(id: number) {
    this.hideError.emit({
      id,
      stepName: this.stateService.currentStep?.stepName || '',
    });
  }

  getFormGroup(field: PaymentFormItem): FormGroup {
    return <FormGroup>this.form.get(field.parent as string) || this.form;
  }
}
