import { Component, OnInit, OnDestroy } from '@angular/core';
import { AbstractControl } from '@angular/forms';
import { PaymentFormFieldHooks } from '@backbase/payment-orders-ang';
import { registerFormControl } from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';
import { ActivatableFormField } from '../../models/activatable-form-field';
import { VisibilityService } from '../../services/visibility.service';

@Component({
  selector: 'bb-payord-country-selector',
  templateUrl: 'payord-country-selector.component.html',
})
export class PayordCountrySelectorComponent
  extends ActivatableFormField
  implements OnInit, OnDestroy
{
  /**
   * Form control
   */
  control!: AbstractControl;

  /**
   * Default country value
   */
  defaultCountry!: string;

  constructor(private readonly visibilityService: VisibilityService) {
    super(visibilityService);
  }

  /**
   * Initialize the component.
   *
   * @returns
   */
  ngOnInit() {
    this.defaultCountry = this.options.defaultValue || '';
    this.control = registerFormControl(
      this.group,
      this.config.name,
      this.options.validators,
      this.options.asyncValidators,
      this.defaultCountry,
      this.config
    );

    this.toggleDependants();
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  /**
   * Cleanup just before Angular destroys the component.
   *
   * @returns
   */
  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }

  /**
   * Selects an item
   *
   * @param country Item country
   * @returns
   */
  selectItem(country: string) {
    this.control.setValue(country);
    this.toggleDependants();
  }
}
