import { FormGroup } from '@angular/forms';
import { PaymentOption as PaymentOptionResponse } from '@backbase/data-ang/payment-order-options';
import {
  PaymentComponents,
  PaymentFormFieldConfig,
  PaymentFormGroup,
  PaymentFormItem,
  PaymentOptionFields,
  PaymentTypeConfig,
} from '@backbase/payment-orders-ang';
import { Observable } from 'rxjs';

export const filterPaymentConfigs = (
  paymentOptions: PaymentOptionResponse[],
  paymentTypes: Array<PaymentTypeConfig>
): PaymentTypeConfig[] =>
  paymentOptions.map(
    (paymentOption) =>
      paymentTypes.filter(
        (paymentConfig) =>
          paymentOption.paymentType === paymentConfig.paymentType
      )[0]
  );

export const getPaymentCostFieldConfig = () => ({
  name: PaymentOptionFields.paymentCost,
  type: PaymentComponents.paymentCost,
  options: {
    isWizardMode: true,
  },
});

export const getSelectPaymentOptionFieldConfig = (
  items$: Observable<Array<PaymentOptionResponse>>,
  paymentTypes: Array<PaymentTypeConfig>,
  checkClosedPaymentsAccess = false
) => {
  const optionLabels = paymentTypes.reduce(
    (nameMap: { [key: string]: string }, paymentConfig: PaymentTypeConfig) => {
      nameMap[paymentConfig.paymentType] = paymentConfig.name;
      return nameMap;
    },
    {}
  );
  return {
    name: PaymentOptionFields.selectPaymentOption,
    type: PaymentComponents.selectPaymentOption,
    options: {
      items$,
      optionLabels,
      isWizardMode: true,
      checkClosedPaymentsAccess,
    },
  };
};

export const getChargeBearerFieldConfig = () => ({
  name: PaymentOptionFields.chargeBearer,
  type: PaymentComponents.chargeBearer,
  options: {
    label: 'How fees are paid',
    isWizardMode: true,
  },
});

export const getRelatedPaymentConfig = (
  paymentConfigs: PaymentTypeConfig[]
): PaymentTypeConfig | undefined =>
  paymentConfigs.filter(
    (paymentConfig) => paymentConfig && !!paymentConfig.relatedPaymentTypes
  )[0];

export const getMappedGroup = (
  mappedGroupName: string,
  form: FormGroup
): FormGroup => {
  let mappedGroup = form.get(mappedGroupName);
  let parent = form;
  const subGroups = mappedGroupName.split('.');

  if (mappedGroup) return <FormGroup>mappedGroup;

  for (const subGroup of subGroups) {
    if (!parent.get(subGroup)) {
      parent.addControl(subGroup, new FormGroup({}));
    }
    mappedGroup = <FormGroup>parent.get(subGroup);
    parent = <FormGroup>mappedGroup;
  }

  return <FormGroup>mappedGroup;
};

/**
 * Tags each form field in a payment config to the corresponding parent form Group.
 */
export const tagFormGroupToFormFields = (
  arr: Array<PaymentFormFieldConfig | PaymentFormGroup>,
  _group: FormGroup,
  controlsArr: Array<PaymentFormItem>,
  form: FormGroup,
  parentName = ''
) => {
  arr.forEach((control: PaymentFormFieldConfig | PaymentFormGroup) => {
    const mappedField = control.options && control.options.mapToField;
    const group = mappedField ? getMappedGroup(mappedField, form) : _group;
    const parent = mappedField || parentName;
    const subArray: Array<PaymentFormItem> = [];

    if ((<PaymentFormGroup>control).fields instanceof Array) {
      const subGroup = <FormGroup>group.get(control.name) || new FormGroup({});
      tagFormGroupToFormFields(
        (<PaymentFormGroup>control).fields,
        subGroup,
        subArray,
        form,
        parent ? `${parent}.${control.name}` : control.name
      );
      group.addControl(control.name, subGroup);
      controlsArr.push({ ...control, fields: subArray, parent: parentName });
    } else {
      controlsArr.push({ ...control, parent });
    }
  });
};

export const hasSameFields = (paymentConfigs: PaymentTypeConfig[]) => {
  let similar = false;
  const relatedTypeConfig = getRelatedPaymentConfig(paymentConfigs);
  if (relatedTypeConfig && relatedTypeConfig.relatedPaymentTypes) {
    const relatedPaymentTypes = [
      relatedTypeConfig.paymentType,
      ...relatedTypeConfig.relatedPaymentTypes,
    ];
    similar = paymentConfigs.every(
      (config) => relatedPaymentTypes.indexOf(config.paymentType) !== -1
    );
  } else {
    similar = paymentConfigs.every(
      (config) => config.paymentType === paymentConfigs[0].paymentType
    );
  }
  return similar;
};
