import {
  Component,
  ElementRef,
  OnDestroy,
  OnInit,
  ViewChild,
} from '@angular/core';
import {
  AbstractControl,
  FormGroup,
  ValidatorFn,
  Validators,
} from '@angular/forms';
import {
  amountValidator,
  currencyValidatorFactory,
  InitiatorFields,
  PaymentBaseFields,
  PaymentFormField,
  PaymentFormFieldConfig,
  PaymentFormFieldHooks,
  PaymentFormFieldOptions,
  PaymentOptionFields,
  RemittanceInfoFields,
} from '@backbase/payment-orders-ang';
import { combineLatest, Subject, Subscription } from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  map,
  startWith,
  tap,
} from 'rxjs/operators';
import { getParentForm, registerFormControl } from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';

export interface CalculatedRate {
  currencyFrom: string;
  currencyTo: string;
  rate: number;
  calculatedAmount: number;
}

@Component({
  selector: 'bb-sanctioned-currency-amount',
  templateUrl: './payord-sanctioned-currency-amount.component.html',
})
export class PayordSanctionedCurrencyAmountComponent
  implements OnInit, PaymentFormField, OnDestroy
{
  private static _id = 0;
  readonly sanctionedCurrencyAmountId =
    PayordSanctionedCurrencyAmountComponent._id++;

  @ViewChild('amountLabel')
  private amountLabelEl: ElementRef | undefined;

  @ViewChild('helperText')
  private helperTextEL: ElementRef | undefined;

  /**
   * Form config
   */
  config!: PaymentFormFieldConfig;

  /**
   * Form group
   */
  group!: FormGroup;

  /**
   * Form field options
   */
  options!: PaymentFormFieldOptions;

  /**
   * Form control
   */
  control!: AbstractControl;

  /**
   * Subscriptio to a value change
   */
  valueChangeSubscription?: Subscription;

  /**
   * Currency control
   */
  initiatorCurrencyControl!: AbstractControl;

  /**
   * Subject for exchange rate
   */
  exchangeRate$!: Subject<number>;

  /**
   * Control for exchange rate
   */
  exchangeRateControl!: AbstractControl;

  /**
   * Parent form group
   */
  parentForm!: FormGroup;

  /**
   * Initial amount value
   */
  initialAmount = {
    [RemittanceInfoFields.amount]: '',
    [RemittanceInfoFields.currency]: '',
  };

  /**
   * Initializes the component.
   *
   * @returns
   */
  ngOnInit() {
    this.parentForm = getParentForm(this.group);
    this.control = registerFormControl(
      this.group,
      this.config.name,
      [
        Validators.required,
        amountValidator as ValidatorFn,
        currencyValidatorFactory(),
      ],
      this.options.asyncValidators,
      this.options.defaultValue || this.initialAmount,
      this.config
    );

    this.initiatorCurrencyControl = <AbstractControl>(
      this.parentForm.get(
        `${PaymentBaseFields.initiator}.${InitiatorFields.currency}`
      )
    );
    this.exchangeRateControl =
      this.parentForm.get(PaymentOptionFields.exchangeRate) ||
      registerFormControl(this.parentForm, PaymentOptionFields.exchangeRate);

    if (
      this.initiatorCurrencyControl &&
      this.control &&
      !this.options.isTemplateMode
    ) {
      // Listen for "from" and "to" currency values
      this.valueChangeSubscription = combineLatest([
        this.initiatorCurrencyControl.valueChanges.pipe(
          startWith(this.initiatorCurrencyControl.value),
          distinctUntilChanged(),
          filter((val) => !!val)
        ),
        this.control.valueChanges.pipe(
          startWith(this.control.value),
          map(({ currency }) => currency),
          distinctUntilChanged(),
          filter((val) => !!val)
        ),
      ]).subscribe(([fromCurrency, toCurrency]) => {
        this.options.getExchangeRate$.next({ fromCurrency, toCurrency });
      });
    }

    if (this.options.exchangeRate$) {
      this.exchangeRate$ = this.options.exchangeRate$.pipe(
        tap((rate) => {
          this.exchangeRateControl.setValue(rate);
        })
      );
    }

    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  /**
   * Returns the calculated rate
   *
   * @param [exchangeRate] Exchange rate
   */
  getCalculatedRate(exchangeRate = 0): CalculatedRate | undefined {
    if (
      exchangeRate &&
      this.initiatorCurrencyControl &&
      this.control.value.amount
    ) {
      return {
        currencyFrom: this.initiatorCurrencyControl.value,
        currencyTo: this.control.value.currency,
        rate: exchangeRate,
        calculatedAmount: this.control.value.amount / exchangeRate,
      };
    }
    return;
  }

  /**
   * Cleanups just before Angular destroys the component.
   *
   * @returns
   */
  ngOnDestroy() {
    if (this.valueChangeSubscription) {
      this.valueChangeSubscription.unsubscribe();
    }
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }

  getLabelledbyForSelector(): string {
    return (
      this.options?.labelledBy ||
      `${this.amountLabelEl?.nativeElement.id || ''} ${
        this.helperTextEL?.nativeElement.id || ''
      }`.trim()
    );
  }
}
