import {
  Directive,
  Input,
  KeyValueDiffers,
  KeyValueDiffer,
  Renderer2,
  ElementRef,
  DoCheck,
  KeyValueChanges,
} from '@angular/core';

interface KeyValueMap {
  [key: string]: any;
}

@Directive({
  selector: '[bbDynamicAriaAttribute]',
})
export class DynamicAriaAttributeDirective implements DoCheck {
  /**
   * The map that contains a key value pair that contains the an id
   * and a condition when they should be added to the element.
   */
  @Input('bbDynamicAriaAttribute')
  set ariaAttributeMap(value: KeyValueMap) {
    this._rawMap = value;
    if (this._rawMap) {
      this.setAriaAttributeIds(this._rawMap);
      this._keyValueDiffer = null;
      this._keyValueDiffer = this.keyValueDiffers.find(this._rawMap).create();
    }
  }

  /**
   * Flag to denote if the id of the input should be prepended to provided aria ids
   */
  @Input() prependInputId = true;

  /**
   * The selector to select the actual html input.
   */
  @Input() inputSelector = '.form-control';

  /**
   * The name of the aria attribute to be added dynamically. Defaults to aria-describedby.
   */
  @Input() ariaAttributeName = 'aria-describedby';

  private _keyValueDiffer: KeyValueDiffer<string, any> | null = null;
  private _rawMap: KeyValueMap | null = null;

  constructor(
    private readonly keyValueDiffers: KeyValueDiffers,
    private readonly hostElem: ElementRef,
    private readonly renderer: Renderer2
  ) {}

  ngDoCheck() {
    if (this._keyValueDiffer) {
      const keyValueChanges = this._keyValueDiffer.diff(
        this._rawMap as { [k: string]: any }
      );
      if (keyValueChanges) {
        this.applyKeyValueChanges(keyValueChanges);
      }
    }
  }

  private applyKeyValueChanges(changes: KeyValueChanges<string, any>): void {
    const currentMap: KeyValueMap = {};
    changes.forEachAddedItem(
      (record) => (currentMap[record.key] = record.currentValue)
    );
    changes.forEachChangedItem(
      (record) => (currentMap[record.key] = record.currentValue)
    );
    changes.forEachRemovedItem((record) => {
      if (record.previousValue) {
        currentMap[record.key] = false;
      }
    });
    this.setAriaAttributeIds(currentMap);
  }

  private setAriaAttributeIds(idMap: KeyValueMap) {
    const targetElem = this.hostElem.nativeElement.querySelector(
      this.inputSelector
    );
    const targetElemId = targetElem?.id || '';
    const ids = Object.keys(idMap).reduce((acc: string[], id: string) => {
      if (!!idMap[id]) {
        acc.push(this.prependInputId ? targetElemId + id : id);
      }
      return acc;
    }, []);

    this.removeAriaAttributeIds(targetElem);
    const descByIds = ids.join(' ').trim();
    if (targetElem && descByIds) {
      this.renderer.setAttribute(targetElem, this.ariaAttributeName, descByIds);
    }
  }

  private removeAriaAttributeIds(targetElem: HTMLElement) {
    if (targetElem) {
      this.renderer.removeAttribute(targetElem, this.ariaAttributeName);
    }
  }
}
