/**
 * Registers a FormControl in the provided FormGroup
 *
 * @param group
 * @param formControlName
 * @param [validators]
 * @param [asyncValidators]
 * @param [initialValue='']
 * @returns
 */
export function registerFormControl(
  group: FormGroup,
  formControlName: string,
  validators?: Array<ValidatorFn>,
  asyncValidators?: Array<AsyncValidatorFn>,
  initialValue: any = '',
  config?: PaymentFormFieldConfig
): AbstractControl {
  let formControl: AbstractControl;
  formControl = group.controls[formControlName];
  group.addControl(
    formControlName,
    new FormControl(initialValue, validators || [], asyncValidators || [])
  );

  if (formControl) {
    resetValidators(formControl, validators, asyncValidators, config);
  } else {
    group.addControl(
      formControlName,
      new FormControl(initialValue, validators || [], asyncValidators || [])
    );
    formControl = group.controls[formControlName];
  }

  return formControl;
}

/**
 * Converts the nested form array into a flat map. It is used to lookup any fields easily
 */
export const createControlsMap = (
  controls: Array<PaymentFormFieldConfig | PaymentFormGroup>,
  controlsMap: { [key: string]: PaymentFormFieldConfig | PaymentFormGroup } = {}
) => {
  let path: string;
  return controls.reduce(
    (
      acc: { [key: string]: PaymentFormFieldConfig | PaymentFormGroup },
      control: PaymentFormFieldConfig | PaymentFormGroup
    ) => {
      path = control.parent
        ? `${control.parent}.${control.name}`
        : control.name;
      if ((<PaymentFormGroup>control).fields) {
        acc = {
          ...acc,
          ...createControlsMap((<PaymentFormGroup>control).fields, acc),
        };
      } else {
        acc[path] = control;
      }
      return acc;
    },
    controlsMap
  );
};

export function registerFormGroup(
  group: FormGroup,
  formGroupName: string,
  formGroupConfig: { [key: string]: any[] },
  validators?: Array<ValidatorFn>,
  asyncValidators?: Array<AsyncValidatorFn>
): AbstractControl | null {
  const subGroup =
    <FormGroup>group.get(formGroupName) ||
    new FormGroup({}, validators || [], asyncValidators || []);

  for (const key in formGroupConfig) {
    if (formGroupConfig.hasOwnProperty(key)) {
      subGroup.registerControl(
        key,
        new FormControl(
          formGroupConfig[key].shift(),
          (formGroupConfig[key].shift() as Array<ValidatorFn>) || []
        )
      );
    }
  }
  group.addControl(formGroupName, subGroup);

  return group.get(formGroupName);
}

export function getParentForm(control: AbstractControl): FormGroup {
  let ancestor = control;

  while (ancestor && ancestor.parent) {
    ancestor = ancestor.parent;
  }

  return <FormGroup>ancestor;
}

export function getFormFieldData({
  formControlPath,
  form,
  config,
}: {
  formControlPath: string;
  form: FormGroup;
  config: PaymentTypeConfig;
}):
  | {
      control: AbstractControl | null;
      fieldConfig: PaymentFormFieldConfig | PaymentFormGroup | undefined;
    }
  | undefined {
  const apiFieldMap: { [key: string]: any } = {
    'transferTransactionInformation.counterpartyAccount.identification.identification':
      {
        name: CounterPartyFields.accountNumber,
        path: `${PaymentBaseFields.counterparty}.${CounterPartyFields.accountNumber}`,
      },
    'transferTransactionInformation.counterpartyBank.bankBranchCode': {
      name: CounterPartyFields.bankBranchCode,
      path: `${PaymentBaseFields.counterparty}.${CounterPartyFields.creditorBank}.${CounterPartyFields.bankBranchCode}`,
    },
    'transferTransactionInformation.instructedAmount.currencyCode': {
      name: RemittanceInfoFields.amountCurrencyGroup,
      path: `${PaymentBaseFields.remittanceInfo}.${RemittanceInfoFields.amountCurrencyGroup}`,
    },
  };
  const fieldData = apiFieldMap[formControlPath] || null;

  if (fieldData) {
    const control = form.get(fieldData.path);
    const fieldConfig = getConfig(fieldData.name, config);
    return { control, fieldConfig };
  }
  return;
}

export function applyInputFormSettingForCountry({
  inputFormSetting,
  form,
  config,
}: {
  inputFormSetting: InputFormField | any;
  form: FormGroup;
  config: PaymentTypeConfig;
}) {
  const counterpartyGroup = <FormGroup>form.get(PaymentBaseFields.counterparty);
  const isClosedPaymentControl = <FormGroup>(
    form.get(HiddenFields.isClosedPayment)
  );
  const hasArrangementIdControl = <FormGroup>(
    counterpartyGroup.get(CounterPartyFields.id)
  );
  const schemeControl =
    counterpartyGroup.get(CounterPartyFields.schemeName) ||
    registerFormControl(counterpartyGroup, CounterPartyFields.schemeName);
  if (inputFormSetting.apiField) {
    const { control = null, fieldConfig = null } =
      getFormFieldData({
        formControlPath: inputFormSetting.apiField,
        form,
        config,
      }) || {};

    let validators: ValidatorFn[] | null = [];
    if (inputFormSetting.mandatory) {
      // For currency input we need amountValidator instead of Validators.required
      if (inputFormSetting.key === 'currency') {
        validators.push(amountValidator as ValidatorFn);
      } else {
        validators.push(Validators.required);
      }
    }

    if (inputFormSetting.regex) {
      validators.push(createValidatorFromRegex(inputFormSetting.regex));
    }

    if (inputFormSetting.key === 'currency' && inputFormSetting.values.length) {
      validators.push(currencyValidatorFactory(inputFormSetting.values));
    }

    if (inputFormSetting.key === 'account-number') {
      schemeControl.setValue(inputFormSetting.format);
      if (inputFormSetting.format === 'IBAN') {
        validators.push(ibanCountryValidator());
      }
    }

    if (control && fieldConfig) {
      const labelData = inputFormSetting.label || {};
      const labelKey = Object.keys(labelData)[0];
      const label = labelData[labelKey]; // need  to implement i18n for this
      fieldConfig.options = fieldConfig.options || {};

      if (label) {
        if (!fieldConfig.options.defaultLabel) {
          fieldConfig.options.defaultLabel =
            getLabelFromInputSetting(
              form.get('defaultBankCodeSetting')?.value
            ) || fieldConfig.options.label;
        }
        fieldConfig.options.label = label;
      } else if (
        fieldConfig.options.defaultLabel &&
        fieldConfig.options.defaultLabel !== fieldConfig.options.label
      ) {
        // reset to default label if the inputform setting doesn't contain label.
        fieldConfig.options.label = fieldConfig.options.defaultLabel;
      }

      const shouldEnableControls = !(
        isClosedPaymentControl?.value || hasArrangementIdControl?.value
      );
      if (inputFormSetting.notApplicable) {
        control.reset();
        validators = null;
        control.disable();
      } else if (control.disabled && shouldEnableControls) {
        control.enable();
      }

      if (isControlFilled(control)) {
        control.markAsTouched();
      }
      control.setValidators(validators);
      control.updateValueAndValidity();
    }
  }
}

export function setAdditionalBankDetails(
  form: FormGroup,
  config: PaymentTypeConfig
) {
  const setting = <InputFormField>form.get('defaultBankCodeSetting')?.value;
  if (!setting) return;

  const label = getLabelFromInputSetting(setting);
  const regex = setting.regex;
  if (label) {
    setAdditionalBankLabel(CounterPartyFields.correspondentBank, label, config);
    setAdditionalBankLabel(CounterPartyFields.intermediaryBank, label, config);
  }

  if (regex) {
    appendBankCodePatternValidator(
      CounterPartyFields.correspondentBank,
      regex,
      config
    );
    appendBankCodePatternValidator(
      CounterPartyFields.intermediaryBank,
      regex,
      config
    );
  }
}

export function createValidatorFromRegex(pattern: string): ValidatorFn {
  if (pattern.startsWith('/') && pattern.endsWith('/')) {
    pattern = pattern.slice(1, -1);
  }

  return Validators.pattern(new RegExp(pattern));
}

function appendBankCodePatternValidator(
  controlName: string,
  regex: string,
  config: PaymentTypeConfig
) {
  const additionalBankConfig = getConfig(
    controlName,
    config
  ) as PaymentFormGroup;
  if (!additionalBankConfig) return;
  const bankCodeConfig = getConfig(
    CounterPartyFields.bankBranchCode,
    additionalBankConfig
  );
  if (bankCodeConfig) {
    if (!bankCodeConfig?.options) {
      bankCodeConfig.options = {};
    }
    bankCodeConfig.options.validators = [createValidatorFromRegex(regex)];
  }
}

function getLabelFromInputSetting(setting?: InputFormField): string {
  return Object.values(setting?.label || {})[0];
}

function setAdditionalBankLabel(
  additionalBankType: string,
  label: string,
  config: PaymentTypeConfig
) {
  const additionalBankConfig = getConfig(
    additionalBankType,
    config
  ) as PaymentFormGroup;
  if (additionalBankConfig) {
    const bankCodeConfig = getConfig(
      CounterPartyFields.bankBranchCode,
      additionalBankConfig
    );
    const selectAdditionalBankCodeTypeConfig = getConfig(
      'selectAdditionalBankCodeType',
      additionalBankConfig
    );
    const selectAdditionalBankCodeTypeOptions =
      selectAdditionalBankCodeTypeConfig?.options?.options;
    if (bankCodeConfig?.options) {
      bankCodeConfig.options.label = label;
    }
    if (selectAdditionalBankCodeTypeOptions?.length) {
      selectAdditionalBankCodeTypeOptions.forEach(
        (option: { label: string; value: string }) => {
          if (option.value === 'BRANCH_CODE') {
            option.label = label;
          }
        }
      );
    }
  }
}

export function isControlFilled(control: AbstractControl) {
  const isAmountControl =
    (control?.value || {}).hasOwnProperty('currency') &&
    (control?.value || {}).hasOwnProperty('amount');
  return isAmountControl ? !!control?.value?.amount : !!control?.value;
}

export function isClosedPaymentValidator(control: AbstractControl) {
  const form = getParentForm(control);
  const counterPartyDetails = (<FormGroup>(
    form.get(PaymentBaseFields.counterparty)
  ))?.getRawValue();
  const selectedBeneficiary = form.get(HiddenFields.selectedBeneficiary)?.value;
  const isBeneficairyUpdatedManually =
    isSelectedBeneficiaryModified(counterPartyDetails, selectedBeneficiary) ||
    (form?.get(HiddenFields.isManualEdit)?.value &&
      isCounterPartyDetailsTruthy(counterPartyDetails));

  return isBeneficairyUpdatedManually &&
    form?.get(HiddenFields.isClosedPayment)?.value
    ? { hasOnlyClosedPaymentPermission: true }
    : null;
}

export function getCounterpartyFormGroupDetails(
  form: FormGroup
): Partial<ArrangementItem> {
  const counterparty: CounterPartyDetails = (<FormGroup>(
    form.get(PaymentBaseFields.counterparty)
  ))?.getRawValue();

  return {
    name: counterparty?.name,
    accountNumber: counterparty?.accountNumber,
    bankBranchCode: counterparty?.creditorBank?.bankBranchCode,
    BIC: counterparty?.creditorBank?.bic,
  };
}

export function resetValidators(
  formControl: AbstractControl,
  validators?: Array<ValidatorFn>,
  asyncValidators?: Array<AsyncValidatorFn>,
  config?: PaymentFormFieldConfig
) {
  const computedValidators = validators ? [...validators] : [];

  if (!config?.hidden && config?.options?.isConditionalMandatory) {
    computedValidators.push(Validators.required);
  }

  formControl.markAsUntouched();
  formControl.clearValidators();
  formControl.clearAsyncValidators();
  formControl.setAsyncValidators(asyncValidators || []);
  formControl.setValidators(computedValidators);
}

export function addAddressFields(formGroup: FormGroup) {
  const addressFields = {
    addressLine1: [''],
    addressLine2: [''],
    streetName: [''],
    postCode: [''],
    town: [''],
    countrySubDivision: [''],
    country: [''],
  };
  for (const key in addressFields) {
    if (addressFields.hasOwnProperty(key) && !formGroup.get(key)) {
      registerFormControl(formGroup, key);
    }
  }
}

export function registerFormArray(
  group: FormGroup,
  formArrayName: string,
  validators?: Array<ValidatorFn>,
  asyncValidators?: Array<AsyncValidatorFn>
): FormArray {
  const array =
    <FormArray>group.get(formArrayName) ||
    new FormArray([], validators || [], asyncValidators || []);
  group.addControl(formArrayName, array);
  return group.get(formArrayName) as FormArray;
}

export function addControlToFormArray(
  array: FormArray,
  formGroupConfig: any[],
  validators?: Array<ValidatorFn>,
  asyncValidators?: Array<AsyncValidatorFn>,
  initialValue: any = null
): FormArray {
  const subGroup = new FormGroup({}, validators || [], asyncValidators || []);
  formGroupConfig.forEach((formConfig) => {
    if (formConfig.name) {
      subGroup.registerControl(
        formConfig.name,
        new FormControl(
          initialValue,
          (formConfig.validators as Array<ValidatorFn>) || []
        )
      );
    }
  });
  array.push(subGroup);
  return array;
}

export function isEditFormDirty(
  formValue: PaymentDetails,
  initialFormValue?: PaymentDetails
): boolean {
  if (!initialFormValue) return true;
  const schedule = getScheduleDatePart(formValue.schedule);
  const initialSchedule = getScheduleDatePart(initialFormValue.schedule);

  return (
    formValue.initiator?.id !== initialFormValue.initiator?.id ||
    !isCounterpartyObjectEquals(
      formValue.counterparty,
      initialFormValue.counterparty
    ) ||
    !objectEquals(schedule, initialSchedule) ||
    isRemittanceInfoDirty(
      formValue[PaymentBaseFields.remittanceInfo],
      initialFormValue[PaymentBaseFields.remittanceInfo]
    ) ||
    formValue.chargeBearer !== initialFormValue.chargeBearer
  );
}

export function isTemplateFormDirty(
  formValue: PaymentDetails,
  initialFormValue: PaymentDetails
) {
  return (
    formValue[PaymentBaseFields.initiator].id !==
      initialFormValue[PaymentBaseFields.initiator].id ||
    !objectEquals(
      formValue[PaymentBaseFields.counterparty],
      initialFormValue[PaymentBaseFields.counterparty]
    ) ||
    isRemittanceInfoDirty(
      formValue[PaymentBaseFields.remittanceInfo],
      initialFormValue[PaymentBaseFields.remittanceInfo]
    ) ||
    formValue[PaymentTemplateFields.templateName] !==
      initialFormValue[PaymentTemplateFields.templateName]
  );
}

function getScheduleDatePart(schedule: ScheduleItem) {
  return {
    ...schedule,
    startDate: datePart(schedule?.startDate),
    endDate: datePart(schedule?.endDate),
  };
}

function isRemittanceInfoDirty(
  remittanceInfo: RemittanceDetails,
  initialRemittanceInfo: RemittanceDetails
) {
  if (!remittanceInfo) return false;
  const amountGroup = remittanceInfo[RemittanceInfoFields.amountCurrencyGroup];
  const initialAmountGroup =
    initialRemittanceInfo[RemittanceInfoFields.amountCurrencyGroup];

  return (
    amountGroup?.amount !== initialAmountGroup?.amount ||
    amountGroup?.currency !== initialAmountGroup?.currency ||
    remittanceInfo[RemittanceInfoFields.urgent] !==
      initialRemittanceInfo[RemittanceInfoFields.urgent] ||
    remittanceInfo[RemittanceInfoFields.paymentReference] !==
      initialRemittanceInfo[RemittanceInfoFields.paymentReference] ||
    remittanceInfo[RemittanceInfoFields.description] !==
      initialRemittanceInfo[RemittanceInfoFields.description]
  );
}

export function applyPurposeOfPaymentSettings({
  form,
  config,
}: {
  form: FormGroup;
  config: PaymentTypeConfig | any;
}) {
  const country = form.value?.inputFormFromSettings;
  const remittanceGroup = <FormGroup>form.get(PaymentBaseFields.remittanceInfo);
  if (!remittanceGroup) {
    return;
  }
  const purposeControl =
    remittanceGroup.get(RemittanceInfoFields.purposeOfPayment) ||
    registerFormControl(remittanceGroup, RemittanceInfoFields.purposeOfPayment);
  const info = getPurposeOfPaymentInfo(country);
  const fieldConfig = getConfig(RemittanceInfoFields.purposeOfPayment, config);
  const options = fieldConfig?.options;

  if (purposeControl && options) {
    options.isOptional = info.isOptional;
    options.purposes = info.purposes;
    options.isCollapsed = info.isOptional;

    if (options.isOptional) {
      purposeControl.setValidators(null);
      purposeControl.setAsyncValidators(null);
      purposeControl.updateValueAndValidity();
    } else {
      purposeControl.setValidators([
        Validators.required,
        ...(options.validators || []),
      ]);
      purposeControl.setAsyncValidators([...(options.asyncValidators || [])]);
      purposeControl.updateValueAndValidity();
    }
  }
}

function isCounterPartyDetailsTruthy(counterpartyDetails: CounterPartyDetails) {
  return isObjectTruthy({
    name: counterpartyDetails?.name,
    accountNumber: counterpartyDetails.accountNumber,
    bankBranchCode: counterpartyDetails.creditorBank?.bankBranchCode,
  });
}

export function getBeneficiaryDetails(
  beneficiary?: ContactItem | ArrangementItem
): Partial<ArrangementItem & { address: Address; bankAddress: Address }> {
  const contactAccount = (<ContactItem>beneficiary)?.accounts?.[0];
  const beneficiaryItem = beneficiary as ArrangementItem;
  return {
    bankBranchCode: contactAccount?.bankCode || beneficiaryItem?.bankBranchCode,
    accountNumber:
      contactAccount?.accountNumber || beneficiaryItem?.accountNumber,
    name: beneficiary?.name,
    BIC: contactAccount?.BIC || beneficiaryItem?.BIC,
    address: getAccountHolderAddress(contactAccount),
    bankAddress: getContactBankAddress(contactAccount),
  };
}

export function revertBeneficiaryChanges(form: FormGroup): void {
  const counterPartyGroup = <FormGroup>form.get(PaymentBaseFields.counterparty);
  const selectedBeneficiary = form.get(HiddenFields.selectedBeneficiary)?.value;
  const addressGroup = counterPartyGroup.get(CounterPartyFields.postalAddress);
  const bankAddressGroup = counterPartyGroup.get([
    CounterPartyFields.creditorBank,
    CounterPartyFields.postalAddress,
  ]);
  const isManualEditControl = form.get(HiddenFields.isManualEdit);

  if (selectedBeneficiary) {
    const beneficiaryDetails = getBeneficiaryDetails(selectedBeneficiary);
    isManualEditControl?.reset();
    counterPartyGroup.patchValue({
      [CounterPartyFields.name]: beneficiaryDetails.name,
      [CounterPartyFields.accountNumber]: beneficiaryDetails.accountNumber,
      [CounterPartyFields.creditorBank]: {
        [CounterPartyFields.bankBranchCode]: beneficiaryDetails.bankBranchCode,
        [CounterPartyFields.bic]: beneficiaryDetails.BIC,
      },
    });
    addressGroup?.setValue({ ...beneficiaryDetails.address });
    bankAddressGroup?.setValue({
      ...beneficiaryDetails.bankAddress,
      country:
        beneficiaryDetails.bankAddress?.country ||
        form.get(HiddenFields.defaultCountry)?.value,
    });
  } else {
    resetBeneficiaryDetails(counterPartyGroup);
  }
}
