import { Component, OnInit, OnDestroy } from '@angular/core';
import { registerFormControl } from '../../helpers/form-helpers';
import { ActivatableFormField } from '../../models/activatable-form-field';
import { VisibilityService } from '../../services/visibility.service';
import { triggerHook } from '../../helpers/hooks-helpers';
import {
  PaymentFormFieldHooks,
  PaymentOptionFields,
  TemplateModeType,
} from '@backbase/payment-orders-ang';
import { AbstractControl, Validators } from '@angular/forms';
import { startWith, tap } from 'rxjs/operators';
import { BehaviorSubject, Subscription, Observable } from 'rxjs';

@Component({
  selector: 'bb-charge-bearer',
  templateUrl: './charge-bearer.component.html',
})
export class PayordChargeBearerComponent
  extends ActivatableFormField
  implements OnInit, OnDestroy
{
  chargeBearerOptionsControl?: AbstractControl | null;
  chargeBearerOptionsSubject$ = new BehaviorSubject<Array<string>>([]);
  chargeBearerOptions$: Observable<string[]> =
    this.chargeBearerOptionsSubject$.asObservable();
  subscription?: Subscription;

  constructor(private readonly visibilityService: VisibilityService) {
    super(visibilityService);
  }

  ngOnInit() {
    this.control = registerFormControl(
      this.group,
      this.config.name,
      this.options.validators,
      this.options.asyncValidators,
      this.options.defaultValue,
      this.config
    );
    this.chargeBearerOptionsControl = registerFormControl(
      this.group,
      PaymentOptionFields.chargeBearerOptions
    );
    if (this.chargeBearerOptionsControl) {
      this.subscription = this.chargeBearerOptionsControl.valueChanges
        .pipe(
          startWith(this.chargeBearerOptionsControl.value),
          tap((chargeBearerOptions: Array<string> | null) => {
            chargeBearerOptions = chargeBearerOptions || [];
            this.control.setValidators(
              chargeBearerOptions.length ? Validators.required : null
            );
            this.control.updateValueAndValidity();
            if (
              this.control.value &&
              chargeBearerOptions.length &&
              chargeBearerOptions.indexOf(this.control.value) === -1 &&
              this.options.templateModeType !== TemplateModeType.EDIT
            ) {
              this.control.reset();
            }
            this.chargeBearerOptionsSubject$.next(chargeBearerOptions);
          })
        )
        .subscribe();
    }
    this.toggleDependants();
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }
}
