import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  AfterViewInit,
  Renderer2,
  ChangeDetectorRef,
} from '@angular/core';
import {
  FormGroup,
  AbstractControl,
  Validators,
  ValidatorFn,
} from '@angular/forms';
import { combineLatest, fromEvent, Observable, of, Subject } from 'rxjs';
import { map, take, takeUntil, catchError, shareReplay } from 'rxjs/operators';
import {
  PaymentFormField,
  PaymentFormFieldOptions,
  PaymentFormFieldConfig,
  PaymentFormFieldHooks,
  InitiatorFields,
  CounterPartyFields,
  AccountBalances,
  PaymentBaseFields,
  BaseObject,
  ProductKinds,
  FilterState,
  ProductKindItem,
  DisabledCombination,
  InternalAccountFormItem,
} from '@backbase/payment-orders-ang';
import { triggerHook } from '../../helpers/hooks-helpers';
import { registerFormControl } from '../../helpers/form-helpers';
import { ActivatedRoute } from '@angular/router';
import { getRouteParams } from '../../helpers/route-helpers';
import { ProductSummaryItem } from '@backbase/data-ang/arrangements';
import { ActivatableFormField } from '../../models/activatable-form-field';
import { VisibilityService } from '../../services/visibility.service';
import { ProductSelectorComponent } from '@backbase/ui-ang/product-selector';

const getDbsValue = (
  dbsObj: ProductSummaryItem,
  keys: string
): number | string | undefined => {
  const value = keys
    .split('.')
    .reduce((obj, key) => (<BaseObject<any>>obj)?.[key], dbsObj);
  return <number | string | undefined>(
    (['string', 'number'].includes(typeof value) ? value : undefined)
  );
};

@Component({
  selector: 'bb-payord-internal-account-selector',
  templateUrl: 'payord-internal-account-selector.component.html',
})
export class PayordInternalAccountSelectorComponent
  extends ActivatableFormField
  implements PaymentFormField, OnInit, OnDestroy, AfterViewInit
{
  @ViewChild('productSelector') productSelector:
    | ProductSelectorComponent
    | undefined;
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  form!: FormGroup;
  control!: AbstractControl;
  selectedAccount!: InternalAccountFormItem & { number?: string };
  disabledAccount: InternalAccountFormItem | undefined;
  private readonly destroy$ = new Subject<void>();
  connectedAccounts$: Observable<InternalAccountFormItem[]> = of([]);
  readonly productKinds = ProductKinds;
  /**
   * Id to associate messages to form field for accessibility
   */
  validationId = '';
  /**
   * Account number fromat used to mask the account numbers in the selector
   */
  accountFormat = {
    length: 0,
    maskRange: [0, 0],
    segments: 0,
  };

  /**
   * Boolean to show if account selector tab is touched
   */
  touched = false;
  /**
   * Return other account selector field name
   */
  get peerFieldName() {
    return this.config.name === InitiatorFields.initiatorAccountGroup
      ? PaymentBaseFields.counterparty
      : PaymentBaseFields.initiator;
  }
  /**
   * Flag to show if dropdown is open
   */
  get isDropdownOpen(): boolean {
    return !!this.productSelector?.bbDropdown?.isOpen();
  }
  /**
   * Flag to show which account kinds should not be rendered in order to avoid unwanted matches between two account
   */
  get disabledProductKinds(): ProductKinds[] {
    const combinations =
      this.options.paymentTypeOptions?.disabledCombinations || [];
    const isInitiator =
      this.config.name === InitiatorFields.initiatorAccountGroup;
    const counterParty = this.disabledAccount?.productKindName;
    const disabledProductKinds = combinations.filter((i: DisabledCombination) =>
      isInitiator ? i.to === counterParty : i.from === counterParty
    );
    return disabledProductKinds.map((i: DisabledCombination) =>
      isInitiator ? i.from : i.to
    );
  }

  constructor(
    private readonly renderer: Renderer2,
    private readonly cd: ChangeDetectorRef,
    private readonly activatedRoute: ActivatedRoute,
    visibilityService: VisibilityService
  ) {
    super(visibilityService);
  }

  ngOnInit() {
    this.validationId = `account-selector-${this.config.name}-error-message`;

    this.form = <FormGroup>(this.group.parent ? this.group.parent : null);
    this.registerFields();
    this.control = this.group.controls['name'];
    const {
      otherFilters,
      connectedItems$,
      items$,
      productKinds,
      connectedAccounts,
      preselect,
    } = this.options;

    if (connectedAccounts) {
      this.connectedAccounts$ = connectedItems$.pipe(take(1));
    }

    this.options.items$ = items$.pipe(
      take(1),
      map((accounts: InternalAccountFormItem[]) =>
        this.options.mapItems && accounts?.length
          ? this.options.mapItems(accounts)
          : accounts
      ),
      map((accounts: InternalAccountFormItem[]) =>
        this.filterByProductKind(accounts, productKinds)
      ),
      map((accounts: InternalAccountFormItem[]) =>
        this.filterByOtherProps(accounts, otherFilters)
      ),
      map((accounts: InternalAccountFormItem[]) =>
        accounts.map(this.mapBalance.bind(this))
      ),
      shareReplay(1)
    );

    combineLatest([
      <Observable<InternalAccountFormItem[]>>this.options.items$.pipe(
        take(1),
        catchError(() => of([]))
      ),
      <Observable<InternalAccountFormItem[]>>this.connectedAccounts$.pipe(
        take(1),
        catchError(() => of([]))
      ),
    ]).subscribe(([internals, externals = []]) => {
      const accounts = [...internals, ...externals];
      if (!this.group.value['id']) {
        let selection;
        const { transferFrom, transferTo } = getRouteParams(
          this.activatedRoute.root
        );
        const { name: configName } = this.config;
        const idParam =
          configName === InitiatorFields.initiatorAccountGroup
            ? transferFrom
            : transferTo;
        selection = idParam && accounts.find((acc) => acc.id === idParam);
        if (preselect && !selection && transferTo !== accounts[0]?.id) {
          selection = accounts[0];
        }
        if (selection) {
          this.selectItem(selection);
        }
      }
      this.setAccountInEditMode(accounts, this.group, 'id');
    });

    this.setDisabledAccount(this.form);
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngAfterViewInit() {
    if (this.productSelector) {
      const { nativeElement } = this.productSelector.dropdownToggle;
      fromEvent(nativeElement, 'blur')
        .pipe(take(1))
        .subscribe(() => this.setTouched());

      //Here to fix Safari focus issue https://developer.mozilla.org/en-US/docs/Web/HTML/Element/button#Clicking_and_focus
      fromEvent<any>(nativeElement, 'click')
        .pipe(take(1))
        .subscribe((e) => e.currentTarget.focus());

      // Fix accessibility issue (MAINT-10313)
      this.renderer.setAttribute(
        nativeElement,
        'aria-describedby',
        this.validationId
      );
    }
  }

  /**
   * Filtering accounts by product kind
   */
  filterByProductKind(
    accounts: InternalAccountFormItem[] = [],
    productKinds: ProductKindItem[] = []
  ) {
    return accounts?.filter((account: InternalAccountFormItem) =>
      productKinds.length
        ? productKinds.some((i) => i.productKind === account.productKindName)
        : true
    );
  }
  /**
   * Filtering accounts by other filter properties
   */
  filterByOtherProps(
    accounts: InternalAccountFormItem[] = [],
    otherFilters: FilterState[] = []
  ) {
    return accounts.filter((account) =>
      otherFilters.length
        ? otherFilters.every(({ key, value }) => {
            const dbsValue = getDbsValue(
              <ProductSummaryItem>account?.defaultRecord,
              key
            );
            return value.some((v) => v === dbsValue);
          })
        : true
    );
  }

  /**
   * Disable account which is already selected at the counter party selector
   */
  setDisabledAccount(form: FormGroup) {
    if (form?.get(this.peerFieldName)) {
      this.disabledAccount = (form.get(this.peerFieldName) as FormGroup).value;
      (form.get(this.peerFieldName) as FormGroup).valueChanges
        .pipe(takeUntil(this.destroy$))
        .subscribe((acc) => {
          this.disabledAccount = acc;
          this.setPaymentConfig(acc);
        });
    }
  }

  setPaymentConfig(acc: InternalAccountFormItem) {
    if (
      this.config.name === CounterPartyFields.counterPartyAccountGroup &&
      this.form.get(PaymentBaseFields.counterparty)
    ) {
      const role =
        acc.productKindName === ProductKinds.connectedAccounts
          ? 'DEBTOR'
          : 'CREDITOR';
      (this.form.get(PaymentBaseFields.counterparty) as FormGroup).patchValue({
        role,
      });
    }
  }

  setTouched(isTouched = true) {
    this.touched = isTouched;
    if (isTouched) {
      this.group.markAllAsTouched();
    }
    this.cd.detectChanges();
  }

  /**
   * Return paymentType regarding to from-to account combination
   */
  pickPaymentType(): string | undefined {
    const initiatorKind = this.form.value.initiator?.productKindName;
    const counterPartyKind = this.form.value.counterparty?.productKindName;
    const { paymentTypes } = this.options.paymentTypeOptions || {};
    const { connectedAccounts } = ProductKinds;

    if (paymentTypes && initiatorKind && counterPartyKind) {
      return counterPartyKind === connectedAccounts ||
        initiatorKind === connectedAccounts
        ? paymentTypes.external
        : paymentTypes.internal;
    }
    return;
  }

  setAccountInEditMode(
    accounts: InternalAccountFormItem[],
    group: FormGroup,
    key: string
  ) {
    if (accounts && group?.value[key]) {
      const account = accounts.filter((a) => a.id === group.value[key]);
      this.selectItem(account[0]);
    }
  }

  selectAccountGroup(): BaseObject<ValidatorFn[]> | undefined {
    const fieldsGroup =
      this.config.name === InitiatorFields.initiatorAccountGroup
        ? InitiatorFields
        : CounterPartyFields;
    return {
      [fieldsGroup.id]: [Validators.required],
      [fieldsGroup.name]: [Validators.required],
      [fieldsGroup.accountNumber]: [Validators.required],
      [CounterPartyFields.role]: [Validators.required],
      currency: [],
      schemeName: [Validators.required],
      productKindName: [Validators.required],
      accountData: [],
    };
  }

  registerFields() {
    const accountGroup = this.selectAccountGroup();
    for (const key in accountGroup) {
      if (accountGroup.hasOwnProperty(key)) {
        registerFormControl(this.group, key, accountGroup[key], [], '');
      }
    }
  }

  /**
   * Set balance and balance label to account regarding to selected balance type in config
   */
  mapBalance(account: InternalAccountFormItem): InternalAccountFormItem {
    const defaultBalanceType = AccountBalances.current;
    const productKindConfig = this.options?.productKinds?.find(
      (config) => config.productKind === account.productKindName
    );
    const dbsProperty = productKindConfig?.balance?.apiField
      ? productKindConfig.balance.apiField
      : defaultBalanceType;

    const balance = getDbsValue(
      <ProductSummaryItem>account?.defaultRecord,
      dbsProperty
    );
    return {
      ...account,
      balance: <number | undefined>balance,
      balanceLabel: productKindConfig?.balance?.label,
    };
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
    this.destroy$.next();
    this.destroy$.complete();
  }

  selectItem(account: InternalAccountFormItem) {
    if (this.group && account?.name) {
      const { productKindName } = account;
      const { connectedAccounts } = ProductKinds;
      const isPeerConnectedAcc =
        this.disabledAccount?.productKindName === connectedAccounts;
      const schemeName =
        productKindName === connectedAccounts ? 'EXTERNAL_ID' : 'ID';
      const role =
        this.config.name === CounterPartyFields.counterPartyAccountGroup &&
        isPeerConnectedAcc
          ? 'DEBTOR'
          : 'CREDITOR';

      this.group.patchValue({
        id: account.id,
        name: account.name,
        accountNumber: account.accountNumber,
        currency: account.currency,
        schemeName,
        productKindName: account.productKindName,
        role,
        accountData: account,
      });
      this.selectedAccount = {
        ...account,
      };
    }

    this.toggleDependants();

    const paymentType = this.pickPaymentType();
    if (paymentType) {
      this.form.patchValue({ paymentType });
    }

    this.group.markAsDirty();
    this.cd.detectChanges();
  }
}
