import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup } from '@angular/forms';
import {
  Frequencies,
  PaymentFormFieldConfig,
  PaymentFormField,
  PaymentFormFieldOptions,
  Hideable,
  PaymentFormFieldHooks,
  getRelativeDate,
  getDateOnly,
  ScheduleEndType,
} from '@backbase/payment-orders-ang';
import { triggerHook } from '../../helpers/hooks-helpers';

@Component({
  selector: 'bb-payord-schedule-header',
  templateUrl: './payord-schedule-header.html',
})
export class PayordScheduleHeaderComponent
  implements PaymentFormField, OnInit, OnDestroy
{
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig & Hideable;
  group!: FormGroup;
  minOccurrences = 2;
  dateFormat?: string;
  endType = ScheduleEndType;
  frequencies = Frequencies;

  isToday(startDate: Date) {
    const today = getRelativeDate();
    const date = getDateOnly(new Date(startDate));

    return date === today;
  }

  ngOnInit() {
    this.dateFormat = this.options?.dateFormat || 'd MMM y';
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }
}
