import {
  Directive,
  Input,
  OnInit,
  ComponentFactoryResolver,
  ViewContainerRef,
  ComponentRef,
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import {
  PaymentFormField,
  PaymentFormFieldOptions,
  PaymentFormFieldConfig,
} from '@backbase/payment-orders-ang';
import { ComponentsFactoryService } from '../services/components-factory.service';

@Directive({
  selector: '[bbPaymentFormField]',
})
export class PaymentFormFieldDirective implements PaymentFormField, OnInit {
  /**
   * The data of the form field
   */
  @Input() options!: PaymentFormFieldOptions;

  /**
   * The config of form field
   */
  @Input() config!: PaymentFormFieldConfig;

  /**
   * The parent form group of the form field
   */
  @Input() group!: FormGroup;

  component!: ComponentRef<PaymentFormField>;

  constructor(
    private resolver: ComponentFactoryResolver,
    private container: ViewContainerRef,
    private readonly componentsFactory: ComponentsFactoryService
  ) {}

  ngOnInit() {
    const component = this.componentsFactory.getFormField(this.config.type);
    const factory = this.resolver.resolveComponentFactory<any>(component);
    this.component = this.container.createComponent(factory);
    this.component.instance.config = this.config;
    this.component.instance.group = this.group;
    this.component.instance.options = this.options;
  }
}
