import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, AbstractControl } from '@angular/forms';
import {
  PaymentFormFieldConfig,
  PaymentFormField,
  PaymentFormFieldOptions,
  CounterPartyFields,
  Scheme,
  Hideable,
  PaymentFormFieldHooks,
} from '@backbase/payment-orders-ang';
import { registerFormControl } from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';

@Component({
  selector: 'bb-payord-text',
  templateUrl: './payord-text.component.html',
})
export class PayordTextboxComponent
  implements PaymentFormField, OnInit, OnDestroy
{
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig & Hideable;
  group!: FormGroup;
  control!: AbstractControl;

  ngOnInit() {
    this.control = registerFormControl(
      this.group,
      this.config.name,
      this.options.validators,
      this.options.asyncValidators,
      this.options.defaultValue,
      this.config
    );

    if (this.options.isIban) {
      const schemeNameControl =
        this.group.get(CounterPartyFields.schemeName) ||
        registerFormControl(this.group, CounterPartyFields.schemeName);
      if (schemeNameControl.value !== Scheme.IBAN) {
        schemeNameControl.setValue(Scheme.IBAN);
      }
    }

    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }
}
