import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import {
  AbstractControl,
  FormBuilder,
  FormGroup,
  Validators,
} from '@angular/forms';
import {
  PaymentFormFieldConfig,
  PaymentFormGroup,
  PaymentFormItem,
  PaymentTypeConfig,
  PaymentComponents,
  PaymentOptionFields,
  PaymentTemplateFields,
  TemplateModeType,
  getIconByPaymnentType,
  focusErrorElement,
  PaymentMode,
  HiddenFields,
} from '@backbase/payment-orders-ang';
import {
  applyInputFormSettingForCountry,
  applyPurposeOfPaymentSettings,
  registerFormControl,
  setAdditionalBankDetails,
} from '../../helpers/form-helpers';
import { VisibilityService } from '../../services/visibility.service';
import { ComponentsFactoryService } from '../../services/components-factory.service';
import { BehaviorSubject, Subscription } from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  shareReplay,
  startWith,
} from 'rxjs/operators';
import { SanctionedCountry } from '@backbase/data-ang/payment-order-options';

@Component({
  selector: 'bb-payord-form',
  templateUrl: './payord-form.component.html',
  providers: [VisibilityService, ComponentsFactoryService],
})
export class PayordFormComponent implements OnInit, OnDestroy {
  /**
   * The payment type config. Containas the details of all the fields and details of a payment type.
   */
  @Input() config!: PaymentTypeConfig;
  /**
   * Event emitted when the form is submitted.
   */
  @Output() submitted = new EventEmitter();
  /**
   * Event emitted when the form is reset.
   */
  @Output() clear = new EventEmitter();
  /**
   * The flag to denote if the widget is rendered inside a modal.
   */
  @Input() isModalView = false;
  /**
   * The flag to denote if the widget is rendered in payment template mode.
   */
  @Input() isTemplateMode = false;
  /**
   * The flag to denote if it is a debit payment.
   */
  @Input() isDebitPayment = false;
  /**
   * The flag to denote if the widget is rendered in payment edit mode.
   */
  @Input() isEditPaymentModal = false;
  /**
   * The flag to denote if widget should be render the template is edit or create mode.
   */
  @Input() templateModeType?: TemplateModeType;
  /**
   * The actual formgroup item.
   */
  @Input() formItem?: FormGroup;
  /**
   * Flag that denotes if form is being submitted
   */
  @Input() set submitting(isSubmitting: boolean) {
    this.isSubmitting$.next(isSubmitting);
  }
  /**
   * Payment mode type.
   */
  @Input() paymentMode = PaymentMode.CREATE_PAYMENT;
  /**
   * Event emitter when we cancel payment editing (sends form to check if it's not changed)
   */
  @Output() editPaymentCancel = new EventEmitter<FormGroup>();
  /**
   * Event emitter when we submit editing payment (sends form)
   */
  @Output() editPaymentSubmit = new EventEmitter<FormGroup>();

  private isSubmitting$ = new BehaviorSubject<boolean>(false);
  isSubmittingShared$ = this.isSubmitting$.asObservable().pipe(shareReplay(1));
  form!: FormGroup;

  controls: Array<PaymentFormGroup> = [];
  inputFormSettingControl!: AbstractControl;
  templateNameControl!: AbstractControl;
  defaultBankCodeSettingControl!: AbstractControl;
  defaultBankCodeSettingSubscription?: Subscription;
  inputFormSettingSubscription?: Subscription;

  buttonGroupConfig!: PaymentFormFieldConfig;
  paymentModeType = PaymentMode;

  constructor(
    private readonly fb: FormBuilder,
    private readonly componentsFactory: ComponentsFactoryService,
    private readonly formEl: ElementRef
  ) {}

  private scrollToError() {
    focusErrorElement(
      this.formEl?.nativeElement,
      'small.bb-input-validation-message'
    );
  }

  ngOnInit() {
    this.form = this.formItem || this.fb.group({});
    this.buttonGroupConfig = this.getButtonGroupConfig();
    this.componentsFactory.registerCustomFields(this.config.customFields);
    this.createFormFields(this.config.fields, this.form, this.controls);
    registerFormControl(
      this.form,
      'paymentType',
      [],
      [],
      this.config.paymentType
    );
    this.defaultBankCodeSettingControl = registerFormControl(
      this.form,
      'defaultBankCodeSetting'
    );

    this.inputFormSettingControl =
      this.form.get(PaymentOptionFields.inputFormFromSettings) ||
      registerFormControl(this.form, PaymentOptionFields.inputFormFromSettings);
    if (this.isTemplateMode) {
      this.templateNameControl = registerFormControl(
        this.form,
        PaymentTemplateFields.templateName,
        [Validators.required]
      );
      if (this.paymentMode === PaymentMode.EDIT_TEMPLATE) {
        registerFormControl(this.form, PaymentTemplateFields.paymentTemplateId);
      }
    }

    if (this.isDebitPayment) {
      registerFormControl(this.form, HiddenFields.isDebitPayment, [], [], true);
    }

    this.listenDefaultBankCodeSettingChange();
    this.listenFormSettingChange();
  }

  ngOnDestroy() {
    if (this.inputFormSettingSubscription) {
      this.inputFormSettingSubscription.unsubscribe();
    }
    if (this.defaultBankCodeSettingSubscription) {
      this.defaultBankCodeSettingSubscription.unsubscribe();
    }
  }

  createFormFields(
    arr: Array<PaymentFormFieldConfig | PaymentFormGroup>,
    _group: FormGroup,
    controlsArr: Array<PaymentFormItem>,
    parentName?: string
  ) {
    arr.forEach((control: PaymentFormFieldConfig | PaymentFormGroup) => {
      const mappedField = control.options && control.options.mapToField;
      const group = mappedField
        ? <FormGroup>this.form.get(mappedField)
        : _group;
      const parent = mappedField || parentName;
      const subArray: Array<PaymentFormItem> = [];

      if ((<PaymentFormGroup>control).fields instanceof Array) {
        const subGroup =
          <FormGroup>group.get(control.name) || this.fb.group({});
        this.createFormFields(
          (<PaymentFormGroup>control).fields,
          subGroup,
          subArray,
          parent ? `${parent}.${control.name}` : control.name
        );
        group.addControl(control.name, subGroup);
        controlsArr.push({ ...control, fields: subArray, parent: parentName });
      } else {
        controlsArr.push({ ...control, parent });
      }
    });
  }

  getFormGroup(field: PaymentFormItem): FormGroup {
    return <FormGroup>this.form.get(field.parent as string) || this.form;
  }

  getButtonGroupConfig() {
    return {
      type: PaymentComponents.buttonGroup,
      name: PaymentComponents.buttonGroup,
      options: {
        submitting$: this.isSubmitting$,
        isModalView: this.isModalView,
        isTemplateMode: this.isTemplateMode,
        templateModeType: this.templateModeType,
        paymentMode: this.paymentMode,
        submit: this.onSubmit.bind(this),
        reset: this.resetForm.bind(this),
      },
    };
  }

  getIcon() {
    return getIconByPaymnentType(this.config.paymentType);
  }

  onSubmit() {
    this.form.markAsDirty();
    this.form.markAllAsTouched();
    this.form.updateValueAndValidity();
    if (!this.form.valid) {
      this.scrollToError();
    } else if (this.form.valid && !this.submitting) {
      this.submitted.emit(this.form);
    }
  }

  onEditionSubmit() {
    this.form.markAllAsTouched();
    if (this.form.valid) {
      this.editPaymentSubmit.emit(this.form);
    } else {
      this.scrollToError();
    }
  }

  onEditionCancel() {
    this.editPaymentCancel.emit(this.form);
  }

  listenDefaultBankCodeSettingChange() {
    this.defaultBankCodeSettingSubscription =
      this.defaultBankCodeSettingControl.valueChanges.subscribe(() => {
        setAdditionalBankDetails(this.form, this.config);
      });
  }

  listenFormSettingChange() {
    this.inputFormSettingSubscription =
      this.inputFormSettingControl.valueChanges
        .pipe(
          startWith(this.inputFormSettingControl.value),
          distinctUntilChanged(),
          filter((value) => !!value)
        )
        .subscribe((value: SanctionedCountry | any) => {
          for (const inputFormSetting of value.inputFormSettings || []) {
            applyInputFormSettingForCountry({
              inputFormSetting,
              form: this.form,
              config: this.config,
            });
          }
          applyPurposeOfPaymentSettings({
            form: this.form,
            config: this.config,
          });
        });
  }

  resetForm() {
    this.clear.emit();
  }
}
