import { Component, OnInit, OnDestroy } from '@angular/core';
import { AbstractControl, FormControl, FormGroup } from '@angular/forms';
import {
  PaymentFormFieldConfig,
  PaymentFormField,
  PaymentFormFieldOptions,
  Hideable,
  PaymentFormFieldHooks,
  resetBeneficiaryDetails,
  CounterPartyFields,
  PaymentBaseFields,
  InitiatorFields,
  HiddenFields,
} from '@backbase/payment-orders-ang';
import { triggerHook } from '../../helpers/hooks-helpers';
import { registerFormControl } from '../../helpers/form-helpers';

@Component({
  selector: 'bb-payord-beneficiary-header',
  templateUrl: './payord-beneficiary-group-header.component.html',
})
export class PayordBeneficiaryGroupHeaderComponent
  implements PaymentFormField, OnInit, OnDestroy
{
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig & Hideable;
  group!: FormGroup;
  iconName = 'clear';
  hideBeneficiaryFieldsControl!: AbstractControl;
  private initiatorIdControl!: AbstractControl;

  ngOnInit() {
    this.hideBeneficiaryFieldsControl = registerFormControl(
      this.group,
      HiddenFields.hideBeneficiaryFields
    );
    this.initiatorIdControl = <FormControl>(
      this.group.parent?.get([PaymentBaseFields.initiator, InitiatorFields.id])
    );
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  onClearValues() {
    const parentGroup = this.group.parent as FormGroup;
    const nameControl = <FormControl>(
      parentGroup.get([PaymentBaseFields.initiator, InitiatorFields.name])
    );
    if (nameControl.value) {
      nameControl.setErrors(null);
    }
    if (typeof this.options?.clearOptions?.onClear === 'function') {
      this.options.clearOptions.onClear(this.group);
    } else {
      resetBeneficiaryDetails(this.group);
    }

    this.hideBeneficiaryFieldsControl?.setValue(true);
    this.initiatorIdControl?.updateValueAndValidity();
  }

  showClearBtn() {
    const accountName = this.group.get(CounterPartyFields.name);
    const accountNumber = this.group.get(CounterPartyFields.accountNumber);
    const bankBranchCode = this.group.get([
      CounterPartyFields.creditorBank,
      CounterPartyFields.bankBranchCode,
    ]);
    const bic = this.group.get([
      CounterPartyFields.creditorBank,
      CounterPartyFields.bic,
    ]);
    return (
      accountName?.value ||
      accountNumber?.value ||
      bankBranchCode?.value ||
      bic?.value
    );
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }
}
