import { Component, OnInit, OnDestroy, ElementRef } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import {
  PaymentFormFieldConfig,
  PaymentFormFieldOptions,
  PaymentFormField,
  PaymentFormFieldHooks,
  AddressType,
  focusErrorElement,
  PaymentBaseFields,
  CounterPartyFields,
  isAddressTruthy,
} from '@backbase/payment-orders-ang';
import {
  registerFormGroup,
  registerFormControl,
  getParentForm,
} from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { SanctionedCountry } from '@backbase/data-ang/payment-order-options';
import { CountriesMap, ViewType } from '@backbase/payment-orders-ang';

@Component({
  selector: 'bb-payord-address',
  templateUrl: './payord-address.component.html',
})
export class PayordAddressComponent
  implements PaymentFormField, OnInit, OnDestroy
{
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  options: PaymentFormFieldOptions = {};
  countries$!: Observable<{ label: string; value: string | undefined }>;

  addressViewType = ViewType.FORM;
  addressGroup!: FormGroup;
  initialAddress = {
    addressLine1: [''],
    addressLine2: [''],
    streetName: [''],
    postCode: [''],
    town: [''],
    countrySubDivision: [''],
  };
  parentForm!: FormGroup;
  counterpartyIdControl!: AbstractControl;

  constructor(private readonly elRef: ElementRef) {}

  ngOnInit() {
    this.options.hideCountry =
      this.options.hideCountry ||
      (this.options.isWizardMode &&
        this.options.addressType === AddressType['beneficiary bank']);
    this.countries$ =
      this.options.useSanctionedCountries || this.options.isWizardMode
        ? this.options.items$.pipe(
            map((items: Array<SanctionedCountry>) =>
              items.map(({ country }: SanctionedCountry) => ({
                value: country,
                label: CountriesMap[country],
              }))
            )
          )
        : of(this.options.countries);

    this.addressGroup = <FormGroup>(
      registerFormGroup(this.group, this.config.name, this.initialAddress)
    );
    if (!this.options.hideCountry) {
      registerFormControl(this.addressGroup, 'country');
    }
    this.addressViewType = isAddressTruthy(this.addressGroup.value)
      ? ViewType.CARD
      : ViewType.BUTTON;
    this.parentForm = getParentForm(this.group);
    this.counterpartyIdControl = <AbstractControl>(
      this.parentForm.get([
        PaymentBaseFields.counterparty,
        CounterPartyFields.id,
      ])
    );
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }

  onAddressAdd() {
    this.addressViewType = ViewType.FORM;
  }

  onAddressEdit() {
    this.addressViewType = ViewType.FORM;
  }

  onAddressDelete() {
    this.addressViewType = ViewType.BUTTON;
    if (this.options.hideCountry || this.options.options?.length === 1) {
      for (const key in this.addressGroup.controls) {
        if (key !== 'country') {
          this.addressGroup.controls[key].reset();
        }
      }
    } else {
      this.addressGroup.reset();
    }
  }

  onAddressReset() {
    this.addressViewType = ViewType.BUTTON;
  }

  onAddressSubmit() {
    if (this.addressGroup.valid) {
      this.addressViewType = ViewType.CARD;
    } else {
      focusErrorElement(this.elRef.nativeElement);
    }
  }
}
