import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  PaymentFormFieldHooks,
  PaymentBaseFields,
  RemittanceInfoFields,
  PaymentOptionFields,
  HiddenFields,
  isSelectedBeneficiaryModified,
} from '@backbase/payment-orders-ang';
import {
  getCounterpartyFormGroupDetails,
  registerFormControl,
  revertBeneficiaryChanges,
} from '../../helpers/form-helpers';
import { ActivatableFormField } from '../../models/activatable-form-field';
import { VisibilityService } from '../../services/visibility.service';
import { triggerHook } from '../../helpers/hooks-helpers';
import {
  AbstractControl,
  FormControl,
  Validators,
  FormGroup,
} from '@angular/forms';
import { PaymentOption } from '@backbase/payment-orders-ang';
import { Observable, of } from 'rxjs';
import { tap, map } from 'rxjs/operators';
import { getDateOnly, isObjectTruthy } from '@backbase/payment-orders-ang';

@Component({
  selector: 'bb-select-payment-option',
  templateUrl: './payord-select-payment-option.html',
})
export class PayordSelectPaymentOptionComponent
  extends ActivatableFormField
  implements OnInit, OnDestroy
{
  remittanceInfoGroup?: AbstractControl | null;
  paymentOptions!: Array<PaymentOption>;
  paymentOptions$!: Observable<Array<PaymentOption>>;
  paymentTypecontrol!: AbstractControl;
  instructionPriorityControl!: AbstractControl;
  transferFeeControl!: AbstractControl;
  chargeBearerOptionsControl!: AbstractControl;
  paymentTypeNameControl!: AbstractControl;
  separator = '::';
  defaultTransFerFee = {
    amount: '',
    currencyCode: '',
  };
  today = getDateOnly(new Date());
  showConfirmOptionModal = false;
  selectedPaymentOptionLabel?: string;
  private previousPaymentOption?: PaymentOption;
  private currentPaymentOption?: PaymentOption;
  private isClosedPaymentControl!: AbstractControl;
  private isManualEditControl!: AbstractControl;

  constructor(private readonly visibilityService: VisibilityService) {
    super(visibilityService);
  }

  ngOnInit() {
    this.paymentTypecontrol = <FormControl>this.group.get('paymentType');
    this.paymentTypeNameControl = registerFormControl(
      this.group,
      PaymentOptionFields.paymentTypeName
    );
    this.control = registerFormControl(this.group, this.config.name, [
      Validators.required,
    ]);
    this.transferFeeControl =
      this.group.get(PaymentOptionFields.transferFee) ||
      registerFormControl(this.group, PaymentOptionFields.transferFee);
    this.chargeBearerOptionsControl =
      this.group.get(PaymentOptionFields.chargeBearerOptions) ||
      registerFormControl(this.group, PaymentOptionFields.chargeBearerOptions);
    this.remittanceInfoGroup = this.group.get(PaymentBaseFields.remittanceInfo);
    this.isClosedPaymentControl = <FormControl>(
      this.group.get(HiddenFields.isClosedPayment)
    );
    this.isManualEditControl = <FormControl>(
      this.group.get(HiddenFields.isManualEdit)
    );

    if (this.remittanceInfoGroup) {
      this.instructionPriorityControl =
        this.remittanceInfoGroup.get(RemittanceInfoFields.urgent) ||
        registerFormControl(
          <FormGroup>this.remittanceInfoGroup,
          RemittanceInfoFields.urgent
        );
    }

    this.paymentOptions$ = (this.options.items$ || of([])).pipe(
      map((paymentOptions: Array<PaymentOption>) =>
        paymentOptions.map((paymentOption) => ({
          ...paymentOption,
          hasMin:
            paymentOption.executionTimeFrame &&
            typeof paymentOption.executionTimeFrame.min === 'number',
          hasMax:
            paymentOption.executionTimeFrame &&
            typeof paymentOption.executionTimeFrame.max === 'number',
          name:
            paymentOption.name ||
            (this.options.optionLabels || {})[paymentOption.paymentType || ''],
        }))
      ),
      tap((paymentOptions: Array<PaymentOption>) => {
        this.paymentOptions = paymentOptions || [];
        if (paymentOptions && paymentOptions.length === 1) {
          const value = this.getValue(paymentOptions[0]);
          if (value !== this.control.value) {
            this.control.setValue(value);
            this.setPaymentOptionDetails(paymentOptions[0]);
          }
        }
        if (this.control?.value && paymentOptions.length) {
          const [type, instructionPriority] = this.control.value.split(
            this.separator
          );
          const paymentOption = this.getValueByType(type, instructionPriority);
          if (paymentOption) {
            this.setPaymentOptionDetails(paymentOption);
          } else {
            this.control.reset();
          }
        }
      })
    );
    this.toggleDependants();
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  setPaymentOptionDetails(paymentOption: PaymentOption) {
    if (this.instructionPriorityControl) {
      this.instructionPriorityControl.setValue(
        paymentOption.instructionPriority === 'HIGH'
      );
    }
    if (this.options.isWizardMode && this.options.checkClosedPaymentsAccess) {
      this.isClosedPaymentControl?.setValue(!paymentOption.canEditCounterparty);
    }
    this.transferFeeControl.setValue(
      paymentOption.transferFee || this.defaultTransFerFee
    );
    this.chargeBearerOptionsControl.setValue(paymentOption.chargeBearer);
    this.paymentTypecontrol.patchValue(paymentOption.paymentType);
    this.paymentTypeNameControl.setValue(paymentOption.name);
  }

  getValue(option: PaymentOption) {
    return `${option.paymentType}${this.separator}${
      option.instructionPriority || ''
    }`;
  }

  getValueByType(type: string, priority: string): PaymentOption {
    return this.paymentOptions.filter(
      (option: PaymentOption) =>
        option.paymentType === type && option.instructionPriority === priority
    )[0];
  }

  onRadioChange(value: string) {
    this.toggleDependants();
    const [type, instructionPriority] = value.split(this.separator);
    const paymentOption = this.getValueByType(type, instructionPriority);
    this.selectedPaymentOptionLabel = paymentOption.name;
    this.previousPaymentOption = this.currentPaymentOption;
    this.currentPaymentOption = paymentOption;
    if (type && instructionPriority) {
      if (
        this.shouldShowConfirmationModal(!!paymentOption?.canEditCounterparty)
      ) {
        this.showConfirmOptionModal = true;
      } else {
        this.setPaymentOptionDetails(this.currentPaymentOption);
      }
    }
  }

  private shouldShowConfirmationModal(canEditCounterparty: boolean): boolean {
    const counterpartyDetails = (<FormGroup>(
      this.group.get(PaymentBaseFields.counterparty)
    ))?.getRawValue();
    const selectedBeneficiary = this.group.get(
      HiddenFields.selectedBeneficiary
    )?.value;
    const filledBeneficiary = getCounterpartyFormGroupDetails(this.group);
    const isSelectedBeneficiaryChanged =
      selectedBeneficiary &&
      isSelectedBeneficiaryModified(counterpartyDetails, selectedBeneficiary);
    const isClosedPayment =
      this.options.isWizardMode &&
      this.options.checkClosedPaymentsAccess &&
      !canEditCounterparty;
    const isEdittedManually =
      isObjectTruthy(filledBeneficiary) && this.isManualEditControl?.value;

    return (
      isClosedPayment && (isSelectedBeneficiaryChanged || isEdittedManually)
    );
  }

  confirmPaymentOption(): void {
    this.showConfirmOptionModal = false;
    revertBeneficiaryChanges(this.group);
    if (this.currentPaymentOption) {
      this.setPaymentOptionDetails(this.currentPaymentOption);
    }
  }

  cancelPaymentOption(): void {
    this.showConfirmOptionModal = false;
    let prevPaymentType = '';
    this.currentPaymentOption = this.previousPaymentOption;
    if (this.currentPaymentOption) {
      const instructionPriority = this.instructionPriorityControl.value
        ? 'HIGH'
        : 'NORM';
      prevPaymentType = this.paymentTypecontrol.value;
      if (prevPaymentType) {
        this.control.setValue(
          `${prevPaymentType}${this.separator}${instructionPriority}`
        );
      }
    }
    if (!prevPaymentType || !this.previousPaymentOption) {
      this.instructionPriorityControl.reset();
      this.control.reset();
    }
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }
}
