import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import {
  PaymentFormFieldOptions,
  PaymentFormFieldConfig,
  AccountField,
  PaymentFormFieldHooks,
  MultiPaymentAccountFormItem,
} from '@backbase/payment-orders-ang';
import { registerFormControl } from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';
import { takeUntil, tap } from 'rxjs/operators';
import { PaymentCardNumberFormat } from '@backbase/ui-ang/payment-card-number-pipe';

@Component({
  selector: 'bb-multi-payment-account-selector',
  templateUrl: './multipayment-account-selector.component.html',
})
export class MultiPaymentAccountSelectorComponent implements OnInit, OnDestroy {
  private _selectedAccount!: MultiPaymentAccountFormItem;
  private items!: Array<MultiPaymentAccountFormItem>;
  private gc$$ = new Subject<void>();

  public get selectedAccount() {
    return this._selectedAccount;
  }

  public set selectedAccount(account: MultiPaymentAccountFormItem) {
    this._selectedAccount = account;
    this.group?.patchValue(account);
    this.group?.markAsTouched();
    this.group?.markAsDirty();
  }

  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig;
  group!: FormGroup;

  items$!: Observable<Array<MultiPaymentAccountFormItem>>;

  accountFormat: PaymentCardNumberFormat = {
    length: 0,
    maskRange: [0, 0],
    segments: 0,
  };

  accountGroup = {
    [AccountField.id]: [''],
    [AccountField.name]: [''],
    [AccountField.accountNumber]: [''],
    [AccountField.number]: [''],
    [AccountField.accountType]: [''],
    [AccountField.currency]: [''],
    [AccountField.balance]: [''],
    [AccountField.bankBranchCode]: [''],
  };

  ngOnInit() {
    this.registerFields();
    this.items$ = this.options?.items$.pipe(
      tap((items: MultiPaymentAccountFormItem[]) => {
        this.items = items;
        if (this.group?.get('id')?.value) {
          this.selectedAccount = this.findAccount(this.group?.get('id')?.value);
        }
      })
    );
    const initiator = this.group?.parent?.get('initiator');
    initiator?.valueChanges.pipe(takeUntil(this.gc$$)).subscribe({
      next: (account: MultiPaymentAccountFormItem) => {
        this._selectedAccount = this.findAccount(account.id);
      },
    });
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngOnDestroy() {
    this.gc$$.next();
    this.gc$$.complete();
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }

  registerFields() {
    for (const key in this.accountGroup) {
      if (this.accountGroup.hasOwnProperty(key)) {
        const validators = [];
        if (key === AccountField.id || key === AccountField.name) {
          validators.push(Validators.required);
        }
        registerFormControl(this.group, key, validators, [], '');
      }
    }
  }

  selectItem(account: MultiPaymentAccountFormItem) {
    this.selectedAccount = account;
    if (this.options?.accountSelected) {
      this.options?.accountSelected(account);
    }
    this.options?.searchTerm$.next('');
  }

  onBlur() {
    if (this.options?.required) {
      this.group?.markAllAsTouched();
    }
  }

  onSearch($event: string) {
    if (this.options?.searchTerm$) {
      this.options?.searchTerm$.next($event);
    }
  }

  private findAccount(id: string) {
    return this.items.find((item) => item.id === id) || this.group?.value;
  }
}
