import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  NgZone,
  OnDestroy,
  OnInit,
  Renderer2,
  ViewChild,
} from '@angular/core';
import {
  AbstractControl,
  FormArray,
  FormControl,
  FormGroup,
  ValidatorFn,
} from '@angular/forms';
import {
  CreditDebitMixedIndicator,
  ErrorItem,
  InternalAccountFormItem,
  MultiPaymentCard,
  parseString,
  PaymentFormField,
  PaymentFormFieldConfig,
  PaymentFormFieldOptions,
} from '@backbase/payment-orders-ang';
import {
  animationFrameScheduler,
  asapScheduler,
  fromEvent,
  Observable,
  Subject,
  BehaviorSubject,
} from 'rxjs';
import {
  addControlToFormArray,
  registerFormArray,
} from '../../helpers/form-helpers';
import { auditTime, startWith, take, takeUntil } from 'rxjs/operators';
import { BatchPaymentsPutResponse } from '@backbase/data-ang/payment-batch';
import { ViewportRuler } from '@angular/cdk/scrolling';
import '@angular/localize/init';

const SCROLL_SCHEDULER =
  typeof requestAnimationFrame !== 'undefined'
    ? animationFrameScheduler
    : asapScheduler;

@Component({
  selector: 'bb-multi-payment-card',
  templateUrl: './multi-payment-card.component.html',
  styles: [
    `
      .virtual-scroll__container {
        position: relative;
      }

      .virtual-scroll__padding {
        position: absolute;
        top: 0;
        left: 0;
        width: 1px;
        height: 1px;
        transform-origin: 0 0;
        opacity: 0;
      }

      .virtual-scroll__content {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        max-width: 100vw;
        max-height: 100vh;
      }
    `,
  ],
})
export class MultiPaymentCardComponent
  implements PaymentFormField, OnInit, OnDestroy
{
  options!: PaymentFormFieldOptions;
  fieldOptions: Partial<MultiPaymentCard> = {};
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  paymentArrayControl!: FormArray;
  count = new EventEmitter();
  search = new EventEmitter();
  cardConfig: { name: string; validators?: ValidatorFn[] }[] = [
    {
      name: 'counterpartyName',
      validators: [],
    },
    {
      name: 'counterpartyAccountNumber',
      validators: [],
    },
    {
      name: 'id',
      validators: [],
    },
    { name: 'bankBranchCode' },
    { name: 'accountType' },
    { name: 'balance' },
    { name: 'currency' },
    { name: 'description', validators: [] },
    {
      name: 'amount',
      validators: [],
    },
  ];
  readonly hostRef: MultiPaymentCardComponent = this;

  @ViewChild('scroll', { read: ElementRef, static: true }) scroll!: ElementRef;
  @ViewChild('padding', { read: ElementRef, static: true })
  padding!: ElementRef;
  @ViewChild('content', { read: ElementRef, static: true })
  content!: ElementRef;

  public viewportItems: AbstractControl[] = [];

  public subscription!: Observable<any>;

  private readonly destroy$ = new Subject<void>();
  private scrollableElement!: HTMLElement;

  private _items: Array<{
    expanded: boolean;
    dropdownOpen: boolean;
    height: number;
  }> = [];
  private visibleItemsInViewport = 0;
  private bufferedItems = 5;
  private visibleViewport = 0;
  public firstRenderedItemIndex = 0;

  accounts: {
    search$: BehaviorSubject<string>;
    items$: Observable<InternalAccountFormItem[]>;
  }[] = [];

  defaultConfig: MultiPaymentCard = {
    counterpartyName: {
      label: '',
      validationMessageLabel: $localize`:@@multipayment-card.account.validation-message-label:Account`,
      placeholder: '',
      dropdownIcon: 'toggle-down',
      ariaLabel: $localize`:@@multipayment-card.counterparty-name.aria-label:Toggle beneficiary list`,
    },
    amount: {
      label: $localize`:@@multipayment-card.amount.label:Amount`,
      validationMessageLabel: $localize`:@@multipayment-card.amount.validation-message-label:Amount`,
      currency: 'USD',
      maxLength: 10,
      placeholder: '0.00',
    },
    description: {
      placeholder: $localize`:@@multipayment-card.description.placeholder:Enter text...`,
      minLength: 3,
      maxLength: 140,
    },
  };

  private lastBlur = {
    timeoutId: 0,
    itemIndex: -1,
  };

  constructor(
    private readonly renderer: Renderer2,
    private readonly ngZone: NgZone,
    private readonly viewportRuler: ViewportRuler,
    private readonly changeDetector: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.fieldOptions = {
      counterpartyName: {
        ...this.defaultConfig.counterpartyName,
        ...this.options?.fieldsConfig?.counterpartyName,
      },
      amount: {
        ...this.defaultConfig.amount,
        ...this.options?.fieldsConfig?.amount,
      },
      description: {
        ...this.defaultConfig.description,
        ...this.options?.fieldsConfig?.description,
      },
    };
    this._addValidators();
    this.options.helperText = parseString(
      this.options?.helperText || '',
      this.options?.maximumAllowedPayments
    );
    this.paymentArrayControl = registerFormArray(
      this.group,
      this.config.name,
      this.options.validators
    );
    const controls = this.paymentArrayControl.controls;
    controls.forEach((control) => this.setupItem(control));
    const len = Math.min(controls.length, this.bufferedItems * 2);
    for (let i = 0; i < len; i++) {
      this.addRenderedItem(controls[i], i);
    }

    this.scrollableElement = this.config.options.containerRef.nativeElement;

    this.paymentArrayControl.valueChanges
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        while (this.paymentArrayControl.length > this._items.length) {
          const currentIndex = this._items.length;
          const control = this.paymentArrayControl.at(currentIndex);
          this.setupItem(control);
          this.addRenderedItem(control, currentIndex);
        }
        this.onScrollChanged();
      });

    this.viewportRuler
      .change()
      .pipe(startWith(null), takeUntil(this.destroy$))
      .subscribe(() => {
        this.updateViewport();
        this.onScrollChanged();
      });

    this.ngZone.runOutsideAngular(() => {
      fromEvent(this.scrollableElement, 'scroll')
        .pipe(
          startWith(null),
          takeUntil(this.destroy$),
          auditTime(0, SCROLL_SCHEDULER)
        )
        .subscribe(() => this.onScrollChanged());
    });
  }

  ngOnDestroy() {
    this.accounts.forEach((account) => account.search$.complete());
    this.destroy$.next();
    this.destroy$.complete();
  }

  addAccount() {
    this.setupItem();
    addControlToFormArray(this.paymentArrayControl, this.cardConfig);

    const currentIndex = this.paymentArrayControl.length - 1;
    const control = this.paymentArrayControl.at(currentIndex);

    this.addRenderedItem(control, currentIndex);
    this.count.emit(this.paymentArrayControl.length);
    this.onScrollChanged();
  }

  removeAccount(index: number) {
    if (this.options.paymentIdList[index]) {
      this.config.options
        .deletePayment(index)
        .pipe(take(1))
        .subscribe(() => {
          this.removeAccountAt(index);
        });
    } else {
      this.removeAccountAt(index);
    }
  }
  /**
   * will return form with given index in form Array
   *
   * @param index - form array index
   */
  getForm(index: number): FormGroup {
    return this.paymentArrayControl.at(index) as FormGroup;
  }

  /**
   *  method to get the form details.
   *
   * @param index form array index
   * @param name form field
   */
  getControl(index: number, name: string): FormControl {
    return this.paymentArrayControl.at(index).get(name) as FormControl;
  }
  /**
   * patch value on selecting account
   *
   * @param event -  the selected data
   * @param index - form Array index
   */
  onSelectAccount(event: any, index: number) {
    this.getControl(index, 'id')?.patchValue(event.id, { emitEvent: false });
    this.getControl(index, 'counterpartyName')?.patchValue(event.name);
    this.getControl(index, 'counterpartyAccountNumber')?.patchValue(
      event.accountNumber,
      { emitEvent: false }
    );
    this.getControl(index, 'bankBranchCode')?.patchValue(event.bankBranchCode, {
      emitEvent: false,
    });
    this.getControl(index, 'accountType')?.patchValue(event.accountType, {
      emitEvent: false,
    });
    this.getControl(index, 'balance')?.patchValue(event.balance, {
      emitEvent: false,
    });
    this.getControl(index, 'currency')?.patchValue(event.currency, {
      emitEvent: false,
    });
    this.getControl(index, 'counterpartyName')?.markAllAsTouched();
    this.getControl(index, 'counterpartyName')?.markAsDirty();
    this.getControl(index, 'counterpartyName')?.updateValueAndValidity();
    this.getControl(
      index,
      'counterpartyAccountNumber'
    )?.updateValueAndValidity();
    this.cardBlur(index);
  }

  /**
   * Search query will emit search on account selector
   *
   * @param query
   */
  onSearch(index: number, query: string) {
    this.accounts[index].search$.next(query);
    this.search.emit(query);
  }

  openDescription(index: number) {
    this._items[index].expanded = true;
    this.getControl(index, 'description')?.patchValue('', { onlySelf: true });
    this.onScrollChanged();
  }

  isExpanded(index: number) {
    return Boolean(this._items[index] && this._items[index].expanded);
  }

  /**
   * will emit call the blur hook if any
   *
   * @param index - form index
   */
  cardBlur(index: number) {
    this.lastBlur.itemIndex = index;
    this.ngZone.runOutsideAngular(() => {
      this.lastBlur.timeoutId = setTimeout(() => {
        const form = this.getForm(index);
        if (form.dirty && form.valid && this.config.options.paymentBlur) {
          const formValue = form.value;
          const payment = {
            creditDebitIndicator: CreditDebitMixedIndicator.CREDIT,
            counterpartyArrangementId: formValue.id,
            counterpartyName: formValue.counterpartyName,
            counterpartyAccountNumber: formValue.counterpartyAccountNumber,
            counterpartyBankBranchCode: formValue.bankBranchCode,
            instructedAmount: {
              amount: formValue.amount,
              currencyCode: formValue.currency,
            },

            description: formValue.description,
          };
          this.config.options
            .paymentBlur(payment, index)
            .pipe(take(1))
            .subscribe(
              (response: BatchPaymentsPutResponse) => {
                form.markAsPristine();
                this.changeDetector.detectChanges();
              },
              (error: ErrorItem | { type: string }) => {
                if ('message' in error) {
                  form.setErrors({
                    error: { name: 'error', message: error.message },
                  });
                }
                this.changeDetector.detectChanges();
              }
            );
        }
        if (this.options && this.options.hooks && this.options.hooks.onBlur) {
          this.options.hooks.onBlur(form);
        }
      }) as any;
    });
  }

  cardFocus(index: number) {
    if (this.lastBlur.itemIndex === index) {
      clearTimeout(this.lastBlur.timeoutId);
    }
  }

  deleteDescription(index: number) {
    if (this.getControl(index, 'description')?.value) {
      this.getControl(index, 'description')?.markAsDirty();
    }
    this.getControl(index, 'description')?.patchValue('');
    this._items[index].expanded = false;
    this.onScrollChanged();
  }

  onDropdownOpenChange(index: number, isOpen: boolean) {
    this._items[index].dropdownOpen = isOpen;
  }

  isDropdownOpen(index: number) {
    return this._items[index].dropdownOpen;
  }

  private addRenderedItem(control: AbstractControl, index: number) {
    this.viewportItems.push(control);
    this.changeDetector.detectChanges();
    this.cacheItemHeight(
      this.content.nativeElement.children[
        this.content.nativeElement.children.length - 1
      ],
      index
    );
  }

  private removeAccountAt(index: number) {
    this.paymentArrayControl.removeAt(index);
    this._items.splice(index, 1);
    this.updateVisibleItems();
    this.updateDimensions();
    this.count.emit(this.paymentArrayControl.length);
  }

  private calculateAverageItemHeight() {
    if (this._items.length === 0) {
      return 0;
    }
    return Math.ceil(
      this._items.reduce((prev, curr) => prev + curr.height, 0) /
        this._items.length
    );
  }

  private cacheItemHeight(item: HTMLElement, index: number) {
    const currentItemHeight = this.getElementSize(item).height;
    this._items[index].height = currentItemHeight;
  }

  private firstVisibleItemIndex() {
    if (this._items.length === 0) {
      return 0;
    }

    const scrollTop = this.scrollableElement.scrollTop;
    const contentScrollPosition = Math.max(
      0,
      scrollTop - this.scroll.nativeElement.offsetTop
    );
    let index = 0;
    let remainingScroll = contentScrollPosition - this._items[index].height;
    while (remainingScroll > 0 && index < this._items.length) {
      index += 1;
      remainingScroll -= this._items[index].height;
    }

    return index;
  }

  private invalidateVisibleItemsCache() {
    const children = this.content.nativeElement.children;
    const itemsLength = this.content.nativeElement.children.length;
    for (let i = 0; i < itemsLength; ++i) {
      const internalIndex = this.firstRenderedItemIndex + i;
      this.cacheItemHeight(children[i], internalIndex);
    }
    this.changeDetector.markForCheck();
  }

  private onScrollChanged() {
    this.ngZone.runOutsideAngular(() => {
      requestAnimationFrame(() => {
        this.invalidateVisibleItemsCache();
        this.updateVisibleItems();
        this.updateDimensions();
      });
    });
  }

  private updateViewport() {
    this.ngZone.runOutsideAngular(() => {
      requestAnimationFrame(() => {
        const topOffset = Math.max(
          0,
          this.scroll.nativeElement.offsetTop - this.scrollableElement.scrollTop
        );
        /** @TODO get bottom offset dynamically */
        const bottomOffset = 100;
        this.visibleViewport =
          this.scrollableElement.clientHeight - topOffset - bottomOffset;
      });
    });
  }

  private updateVisibleItems() {
    const averageItemHeight = this.calculateAverageItemHeight();
    if (averageItemHeight > 0) {
      this.visibleItemsInViewport = Math.round(
        this.visibleViewport / this.calculateAverageItemHeight()
      );
    } else {
      this.visibleItemsInViewport = 0;
    }
    this.firstRenderedItemIndex = Math.max(
      0,
      this.firstVisibleItemIndex() - this.bufferedItems
    );
    const renderedItemsLength = Math.min(
      this.visibleItemsInViewport + this.bufferedItems * 2,
      this.paymentArrayControl.controls.length
    );
    this.viewportItems = this.paymentArrayControl.controls.slice(
      this.firstRenderedItemIndex,
      this.firstRenderedItemIndex + renderedItemsLength
    );
    this.changeDetector.markForCheck();
    this.changeDetector.detectChanges();
  }

  private updateDimensions() {
    const contentHeight = this.virtualViewportHeight();
    const padding = this._items
      .slice(0, this.firstRenderedItemIndex)
      .reduce((prev, curr) => prev + curr.height, 0);

    this.renderer.setStyle(
      this.padding.nativeElement,
      'transform',
      `scaleY(${contentHeight})`
    );
    this.renderer.setStyle(
      this.scroll.nativeElement,
      'height',
      `${contentHeight}px`
    );
    this.renderer.setStyle(
      this.content.nativeElement,
      'transform',
      `translateY(${padding}px)`
    );
    this.changeDetector.markForCheck();
    this.changeDetector.detectChanges();
  }

  private getElementSize(element: HTMLElement): ClientRect {
    const result = element.getBoundingClientRect();
    const styles: any = getComputedStyle(element);
    const marginTop = parseInt(styles['margin-top'], 10) || 0;
    const marginBottom = parseInt(styles['margin-bottom'], 10) || 0;
    const marginLeft = parseInt(styles['margin-left'], 10) || 0;
    const marginRight = parseInt(styles['margin-right'], 10) || 0;

    return {
      top: result.top + marginTop,
      bottom: result.bottom + marginBottom,
      left: result.left + marginLeft,
      right: result.right + marginRight,
      width: result.width + marginLeft + marginRight,
      height: result.height + marginTop + marginBottom,
    } as ClientRect;
  }

  private virtualViewportHeight() {
    return this._items.reduce((prev, curr) => prev + curr.height, 0);
  }

  private _addValidators() {
    this.cardConfig.forEach((field) => {
      const name = field.name as keyof MultiPaymentCard;
      if (this.fieldOptions[name]) {
        field.validators = (
          this.fieldOptions[name] as PaymentFormFieldOptions
        ).validators;
      }
    });
  }

  private setupItem(control?: AbstractControl) {
    const expanded = control
      ? control.get('description')?.value?.trim()
      : false;
    this._items.push({
      height: 0,
      expanded,
      dropdownOpen: false,
    });
    this.accounts.push(this.config.options.observableAccountsFactory());
  }
}
