import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { SchemeNames } from '@backbase/data-ang/payment-order';
import {
  BeneficiaryList,
  ContactItem,
  ContactScheme,
  CounterPartyFields,
  FetchCreditorItemsParams,
  HiddenFields,
  Hideable,
  ListState,
  PaymentFormFieldConfig,
  PaymentFormFieldHooks,
  PaymentFormFieldOptions,
} from '@backbase/payment-orders-ang';
import { NgbDropdown } from '@ng-bootstrap/ng-bootstrap';
import { Observable, Subject } from 'rxjs';
import { debounceTime, map, takeUntil, tap } from 'rxjs/operators';
import { registerFormControl } from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';

@Component({
  selector: 'bb-payord-contact-selector',
  templateUrl: './payord-contact-selector.component.html',
})
export class PayordContactSelectorComponent implements OnInit, OnDestroy {
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig & Hideable;
  group!: FormGroup;
  contacts$!: Observable<Array<ContactItem> | null>;
  @ViewChild('dd') dropdownItem?: NgbDropdown;
  listState?: ListState;
  listStateType = ListState;
  private inputFocused = false;
  private destroy$ = new Subject();
  private searchQuery = '';
  defaultEmptyStateTitle = $localize`:@@payord.contact.selector.ui.contacts.nothing-found.label:No contacts`;
  defaultEmptyStateSubtitle = $localize`:@@payord.contact.selector.ui.contacts.nothing-found.subtitle:You don't have any contacts to be displayed.`;
  defaultErrorStateTitle = $localize`:@@payord.contact.selector.ui.contacts.error.label:Oops! Something went wrong`;
  defaultErrorStateSubtitle = $localize`:@@payord.contact.selector.ui.contacts.error.subtitle:We are unable to load your contacts Please try again.`;

  ngOnInit() {
    this.registerFields();
    triggerHook(PaymentFormFieldHooks.onInit, this);
    this.listState = ListState.LOADING;

    this.contacts$ = this.options.items$.pipe(
      map((response) =>
        response instanceof HttpErrorResponse ? null : response
      ),
      map((contacts: ContactItem[]) =>
        this.options.mapItems && contacts?.length
          ? this.options.mapItems(contacts)
          : contacts
      ),
      map((contacts: ContactItem[]) =>
        contacts?.map((contact) => ({
          ...contact,
          transferKey: this.getTransferEntry(contact),
        }))
      ),
      tap((contacts: ContactItem[]) => {
        this.setStatus(contacts);
        // Close dropdown if there is no result by search
        if (this.inputFocused && this.searchQuery) {
          contacts && contacts.length
            ? this.dropdownItem?.open()
            : this.dropdownItem?.close();
        }
      })
    );

    // Sets schemeName on value change in accountNumber field
    this.group.valueChanges
      .pipe(
        takeUntil(this.destroy$),
        debounceTime(1000),
        map(({ accountNumber }) => accountNumber)
      )
      .subscribe(this.setScheme.bind(this));
  }

  private registerFields() {
    registerFormControl(
      this.group,
      this.config.name,
      this.options.validators,
      this.options.asyncValidators,
      this.options.defaultValue
    );
    registerFormControl(
      this.group,
      CounterPartyFields.role,
      [],
      [],
      'CREDITOR'
    );
    registerFormControl(this.group, CounterPartyFields.selectedContact, [], []);
    registerFormControl(this.group, CounterPartyFields.schemeName);
    registerFormControl(this.group, HiddenFields.selectedBeneficiary);
  }

  private fetchCreditorItems(params: FetchCreditorItemsParams) {
    if (this.options?.fetchItems$) {
      this.options.fetchItems$.next(params);
    }
  }

  onSelectContact(_event: EventTarget, contact: ContactItem) {
    this.group.get(this.config.name)?.patchValue(contact.name);
    this.group
      .get(CounterPartyFields.accountNumber)
      ?.patchValue(this.getTransferEntry(contact)?.value);
    this.group.get([HiddenFields.selectedBeneficiary])?.patchValue(contact);
    this.dropdownItem?.close();
    this.setContactId(contact);
  }

  handleSearch(query: string) {
    if (this.inputFocused) {
      this.searchQuery = query;
      this.fetchCreditorItems({
        params: { query },
        type: BeneficiaryList.CONTACTS,
      });
      if (!query) {
        this.dropdownItem?.close();
      }
    }
  }

  setStatus(contacts?: ContactItem[]) {
    if (!contacts) {
      this.listState = ListState.ERROR;
    } else if (!contacts.length) {
      this.listState = ListState.EMPTY;
    } else if (contacts.length) {
      this.listState = ListState.LIST;
    }
  }

  onClear() {
    this.group.get(this.config.name)?.reset();
    this.fetchCreditorItems({ type: BeneficiaryList.CONTACTS });
  }

  focusHandler() {
    this.inputFocused = true;
  }

  onBlur() {
    this.inputFocused = false;
  }

  private setContactId(contact: ContactItem) {
    this.group
      .get([CounterPartyFields.selectedContact])
      ?.patchValue({ contactId: contact?.id });
  }

  /**
   * Sets schemeName for payment order request payload
   * Checks entered value with regex pattern to detect if it is email, phone number or accountNumber
   */
  private setScheme(counterPartyIdentification?: string) {
    if (!counterPartyIdentification) return;
    const scheme = this.options.contactSchemes?.find(({ regex }) =>
      regex?.test(counterPartyIdentification)
    );
    let schemeValue: SchemeNames | undefined;
    switch (scheme?.name) {
      case ContactScheme.email:
        schemeValue = SchemeNames.EMAIL;
        break;
      case ContactScheme.phoneNumber:
        schemeValue = SchemeNames.MOBILE;
        break;
      case ContactScheme.accountNumber:
        schemeValue = SchemeNames.BBAN;
        break;
    }
    if (schemeValue) {
      this.group.controls[CounterPartyFields.schemeName].patchValue(
        schemeValue,
        { emitEvent: false }
      );
    }
  }

  /**
   * Return first scheme value of contact. That allows customer to prioritize either email, phone number or account number for a contact.
   * For instance [email, phoneNumber] will return mail if there is a mail value in contact item, otherwise will check phoneNumber.
   * This is
   */
  private getTransferEntry(contact: ContactItem): {
    scheme: ContactScheme;
    value: string | undefined;
  } {
    const defaultScheme = ContactScheme.accountNumber;
    const firstScheme =
      this.options.contactSchemes?.find(
        (scheme) => !!contact.accounts[0][scheme.name]
      )?.name || defaultScheme;
    return { scheme: firstScheme, value: contact.accounts[0][firstScheme] };
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }
}
