import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  ElementRef,
} from '@angular/core';
import {
  FormGroup,
  Validators,
  ValidatorFn,
  AbstractControl,
} from '@angular/forms';
import {
  amountValidator,
  PaymentFormField,
  PaymentFormFieldConfig,
  PaymentFormFieldOptions,
  RemittanceInfoFields,
  PaymentFormFieldHooks,
} from '@backbase/payment-orders-ang';
import { registerFormControl } from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';

@Component({
  selector: 'bb-payord-amount',
  templateUrl: './payord-amount.component.html',
})
export class PayordAmountComponent
  implements OnInit, PaymentFormField, OnDestroy
{
  private static _id = 0;
  readonly payordAmountId = PayordAmountComponent._id++;

  @ViewChild('amountLabel')
  private amountLabelEl: ElementRef | undefined;

  @ViewChild('helperText')
  private helperTextEL: ElementRef | undefined;

  /**
   * Form config
   */
  config!: PaymentFormFieldConfig;

  /**
   * Form group
   */
  group!: FormGroup;

  /**
   * Form field options
   */
  options!: PaymentFormFieldOptions;

  /**
   * Form control
   */
  control!: AbstractControl;

  /**
   * Initial amount value
   */
  initialAmount = {
    [RemittanceInfoFields.amount]: '',
    [RemittanceInfoFields.currency]: '',
  };

  /**
   * Initializes the component.
   *
   * @returns
   */
  ngOnInit() {
    this.control = registerFormControl(
      this.group,
      this.config.name,
      [Validators.required, amountValidator as ValidatorFn],
      this.options.asyncValidators,
      this.options.defaultValue || this.initialAmount,
      this.config
    );
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  /**
   * Cleanups just before Angular destroys the component.
   *
   * @returns
   */
  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }

  getLabelledbyForSelector(): string {
    return (
      this.options?.labelledBy ||
      `${this.amountLabelEl?.nativeElement.id || ''} ${
        this.helperTextEL?.nativeElement.id || ''
      }`.trim()
    );
  }
}
