import { Component, Optional, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import {
  PaymentDetails,
  PaymentFormField,
  PaymentFormFieldConfig,
  PaymentFormFieldOptions,
  PaymentMode,
} from '@backbase/payment-orders-ang';
import { RoutableModalService } from '@backbase/payment-orders-ang';
import { BehaviorSubject } from 'rxjs';
import { isTemplateFormDirty } from '../../helpers/form-helpers';

@Component({
  selector: 'bb-payord-button-group',
  templateUrl: './payord-button-group.component.html',
})
export class PayordButtonGroupComponent implements PaymentFormField, OnInit {
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  confirmModalClose$ = new BehaviorSubject(false);
  paymentMode = PaymentMode;
  private initialFormValue!: PaymentDetails;

  constructor(
    @Optional() private readonly modalService: RoutableModalService
  ) {}

  ngOnInit() {
    this.initialFormValue = this.group?.value;
  }

  onCancel() {
    if (isTemplateFormDirty(this.group.value, this.initialFormValue)) {
      this.confirmModalClose$.next(true);
    } else {
      this.closeModal();
    }
  }

  closeModal() {
    this.confirmModalClose$.next(false);
    this.modalService?.closeModal();
  }
}
