import {
  PaymentFormField,
  PaymentFormFieldOptions,
  PaymentFormFieldConfig,
  DependantItem,
} from '@backbase/payment-orders-ang';
import { FormGroup, AbstractControl } from '@angular/forms';
import { VisibilityService } from '../services/visibility.service';
import { mapDependentItems } from '../helpers/generic-utils';

export class ActivatableFormField implements PaymentFormField {
  /**
   * Payment form field options
   */
  options!: PaymentFormFieldOptions;

  /**
   * Payment form field configuration
   */
  config!: PaymentFormFieldConfig;

  /**
   * Form group object
   * Tracks the value and validity state of a group of FormControl instances
   */
  group!: FormGroup;

  /**
   * Base class for FormControl, FormGroup, and FormArray
   */
  control!: AbstractControl;

  /**
   * @internal
   */
  constructor(private readonly service: VisibilityService) {}

  /**
   * @internal
   */
  private showDependantFields() {
    if (this.config.options && this.config.options.dependants) {
      this.service.showItems(
        this.config.parent,
        this.config.options.dependants
      );
    }
  }

  /**
   * @internal
   */
  private hideDependantFields() {
    if (this.config.options) {
      this.service.hideItems(
        this.config.parent,
        this.config.options.dependants
      );
    }
  }

  /**
   * Tracks if a form item should be shown or not
   */
  toggleDependants() {
    const { options, parent: currentParent } = this.config;
    if (options && options.dependants) {
      if (typeof options.activateDependantsOn === 'function') {
        const toShow =
          options.activateDependantsOn(this.control.value, this.control) || [];
        const toShowMap = mapDependentItems(toShow, currentParent);
        const toHide = mapDependentItems(
          options?.dependants,
          currentParent
        ).map(({ parent, items }: DependantItem) => {
          const toShowItem = toShowMap.find(
            (i: DependantItem) => i.parent === parent
          );
          if (toShowItem) {
            return {
              parent,
              items: items.filter((i) => !toShowItem.items.includes(i)),
            };
          } else {
            return { parent, items };
          }
        });

        mapDependentItems(toShow, currentParent).forEach(
          ({ parent, items }) => {
            if (items.length) {
              this.service.showItems(parent, items);
            }
          }
        );

        mapDependentItems(toHide, currentParent).forEach(
          ({ parent, items }) => {
            if (items.length) {
              this.service.hideItems(parent, items);
            }
          }
        );
      } else if (
        this.control.value === this.config.options.activateDependantsOn
      ) {
        this.showDependantFields();
      } else {
        this.hideDependantFields();
      }
    }
  }
}
