import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  PaymentFormField,
  PaymentFormFieldHooks,
} from '@backbase/payment-orders-ang';
import { registerFormControl } from '../../helpers/form-helpers';
import { VisibilityService } from '../../services/visibility.service';
import { ActivatableFormField } from '../../models/activatable-form-field';
import { triggerHook } from '../../helpers/hooks-helpers';

@Component({
  selector: 'bb-payord-switch',
  templateUrl: './payord-switch.component.html',
})
export class PayordSwitchComponent
  extends ActivatableFormField
  implements PaymentFormField, OnInit, OnDestroy
{
  constructor(private readonly visibilityService: VisibilityService) {
    super(visibilityService);
  }

  ngOnInit() {
    this.control = registerFormControl(
      this.group,
      this.config.name,
      this.options.validators,
      this.options.asyncValidators,
      this.options.defaultValue,
      this.config
    );
    this.toggleDependants();
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }
}
