import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { distinctUntilChanged } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class MultiPaymentService {
  totalAmount$ = new BehaviorSubject<number>(0);
  totalTransfers$ = new BehaviorSubject<number>(0);

  get paymentTotalAmount$(): Observable<number> {
    return this.totalAmount$.asObservable().pipe(distinctUntilChanged());
  }

  get paymentTotalTransfers$(): Observable<number> {
    return this.totalTransfers$.asObservable().pipe(distinctUntilChanged());
  }
}
