import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  FormGroup,
  Validators,
  ValidatorFn,
  AbstractControl,
} from '@angular/forms';
import {
  amountValidator,
  InternalAccountFormItem,
  PaymentFormField,
  PaymentFormFieldConfig,
  PaymentFormFieldOptions,
  RemittanceInfoFields,
  PaymentFormFieldHooks,
  PaymentBaseFields,
} from '@backbase/payment-orders-ang';
import { registerFormGroup } from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';
import { Observable } from 'rxjs';
import { startWith, tap } from 'rxjs/operators';

@Component({
  selector: 'bb-payord-compact-amount-ui',
  templateUrl: './payord-compact-amount.component.html',
})
export class PayordCompactAmountComponent
  implements OnInit, PaymentFormField, OnDestroy
{
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  options!: PaymentFormFieldOptions;
  control!: AbstractControl;
  fromAccountProduct$!: Observable<InternalAccountFormItem | undefined>;
  remittanceInfoFields = RemittanceInfoFields;
  initialAmount = {
    [RemittanceInfoFields.amount]: '',
    [RemittanceInfoFields.currency]: '',
  };
  amountGroup!: FormGroup;
  readonly id = new Date().getTime();

  ngOnInit() {
    this.amountGroup = <FormGroup>registerFormGroup(
      this.group,
      this.config.name,
      {
        [RemittanceInfoFields.amount]: [
          this.options.defaultValue?.amount || this.initialAmount.amount,
        ],
        [RemittanceInfoFields.currency]: [
          this.options.defaultValue?.currency || this.initialAmount.currency,
        ],
      },
      [Validators.required, amountValidator as ValidatorFn],
      this.options.asyncValidators
    );

    this.fromAccountProduct$ = (<FormGroup>(
      this.group.parent?.get(PaymentBaseFields.initiator)
    ))?.valueChanges.pipe(
      startWith(
        (<FormGroup>this.group.parent?.get(PaymentBaseFields.initiator))?.value
      ),
      tap((initiator) =>
        this.amountGroup
          .get(RemittanceInfoFields.currency)
          ?.patchValue(initiator?.currency || this.options.currency)
      )
    );

    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }
}
