import { Component, OnDestroy, OnInit } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import {
  Currency,
  Hideable,
  InitiatorFields,
  PaymentBaseFields,
  PaymentFormFieldConfig,
  PaymentFormFieldOptions,
  PaymentOptionFields,
  RemittanceInfoFields,
} from '@backbase/payment-orders-ang';
import { ChargeBearer, PaymentCostInfo } from '@backbase/payment-orders-ang';
import BigNumber from 'bignumber.js';
import { BehaviorSubject, combineLatest, Subscription } from 'rxjs';
import { distinctUntilChanged, startWith } from 'rxjs/operators';
import { registerFormControl } from '../../helpers/form-helpers';

@Component({
  selector: 'bb-payment-cost',
  templateUrl: './payment-cost.component.html',
})
export class PayordPaymentCostComponent implements OnInit, OnDestroy {
  readonly defaultCurrency = 'USD';
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig & Hideable;
  group!: FormGroup;
  amountControl!: AbstractControl | null;
  fromCurrencyControl!: AbstractControl | null;
  chargeBearerControl!: AbstractControl;
  transferFeeControl!: AbstractControl;
  exchangeRateControl!: AbstractControl;
  instructionPriorityControl!: AbstractControl | null;
  paymentTypeNameControl!: AbstractControl | null;

  paymentCostInfo$ = new BehaviorSubject<PaymentCostInfo | undefined>(
    undefined
  );

  amount!: {
    [RemittanceInfoFields.amount]: string;
    [RemittanceInfoFields.currency]: string;
  };
  subscription?: Subscription;
  fromCurrency = '';
  exchangeRate = 0;
  instructionPriority = false;
  isTotalAmountDisplayed = true;

  ngOnInit() {
    this.fromCurrencyControl = this.group.get(
      `${PaymentBaseFields.initiator}.${InitiatorFields.currency}`
    );
    this.amountControl = this.group.get(
      `${PaymentBaseFields.remittanceInfo}.${RemittanceInfoFields.amountCurrencyGroup}`
    );
    this.instructionPriorityControl = this.group.get(
      `${PaymentBaseFields.remittanceInfo}.${RemittanceInfoFields.urgent}`
    );

    this.transferFeeControl =
      this.group.get(PaymentOptionFields.transferFee) ||
      registerFormControl(this.group, PaymentOptionFields.transferFee);

    this.chargeBearerControl =
      this.group.get(PaymentOptionFields.chargeBearer) ||
      registerFormControl(this.group, PaymentOptionFields.chargeBearer);

    this.exchangeRateControl =
      this.group.get(PaymentOptionFields.exchangeRate) ||
      registerFormControl(this.group, PaymentOptionFields.exchangeRate);

    this.paymentTypeNameControl = this.group.get(
      PaymentOptionFields.paymentTypeName
    );

    this.listenChanges();
  }

  getTotalCost(transferFee?: Currency, calculatedAmount = 0): string {
    const fee = new BigNumber(transferFee?.amount || '0');
    const amount = new BigNumber(calculatedAmount || this.amount.amount || '0');
    return amount.plus(fee).toString();
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  hasForexInfo(fromCurrency = '', toCurrency = '', exchangeRate = 0) {
    return (
      !!fromCurrency &&
      !!toCurrency &&
      fromCurrency !== toCurrency &&
      exchangeRate !== 0
    );
  }

  listenChanges() {
    if (
      this.amountControl &&
      this.fromCurrencyControl &&
      this.instructionPriorityControl
    ) {
      this.subscription = combineLatest([
        this.fromCurrencyControl.valueChanges.pipe(
          startWith(this.fromCurrencyControl.value),
          distinctUntilChanged()
        ),
        this.amountControl.valueChanges.pipe(
          startWith(this.amountControl.value),
          distinctUntilChanged()
        ),
        this.transferFeeControl.valueChanges.pipe(
          startWith(this.transferFeeControl.value),
          distinctUntilChanged()
        ),
        this.chargeBearerControl.valueChanges.pipe(
          startWith(this.chargeBearerControl.value),
          distinctUntilChanged()
        ),
        this.exchangeRateControl.valueChanges.pipe(
          startWith(this.exchangeRateControl.value),
          distinctUntilChanged()
        ),
        this.instructionPriorityControl.valueChanges.pipe(
          startWith(this.instructionPriorityControl.value),
          distinctUntilChanged()
        ),
      ]).subscribe(
        ([
          fromCurrency,
          amount,
          transferFee,
          chargeBearer,
          exchangeRate,
          instructionPriority,
        ]) => {
          this.fromCurrency = fromCurrency;
          this.amount = amount;
          this.exchangeRate = exchangeRate;
          this.instructionPriority = instructionPriority;

          this.isTotalAmountDisplayed =
            !!this.exchangeRate || this.fromCurrency === this.amount.currency;

          const fee = { ...transferFee };

          if (chargeBearer === ChargeBearer.BEN) {
            // If chargebearer is beneficiary then set transfer fee to 0
            fee.amount = '0';
          }

          if (chargeBearer && !this.options?.isTemplateMode) {
            const _amountByRate = new BigNumber(amount.amount || '0').dividedBy(
              exchangeRate
            );
            const calculatedAmount = !!exchangeRate
              ? _amountByRate.toString()
              : amount.amount;

            this.paymentCostInfo$.next({
              transferFee: fee,
              hasForexInfo: this.hasForexInfo(
                fromCurrency,
                amount.currency,
                exchangeRate
              ),
              rate: exchangeRate,
              fromCurrency: this.fromCurrency,
              toCurrency: this.amount.currency,
              calculatedAmount,
            });
          }
        }
      );
    }
  }
}
