import {
  Component,
  Input,
  Output,
  EventEmitter,
  OnChanges,
} from '@angular/core';
import { ErrorItem } from '@backbase/data-ang/payment-order';

@Component({
  selector: 'bb-error-alert',
  templateUrl: './error-alert.component.html',
})
export class ErrorAlertComponent implements OnChanges {
  /**
   * Collection of errors to show in alerts
   */
  @Input() errorList!: ErrorItem[];

  /**
   * Emitted when an alert has been closed
   */
  @Output() hideAlert = new EventEmitter<number>();

  readonly hostRef = this;

  ngOnChanges() {
    if (this.errorList && this.errorList.length > 0) {
      this.errorList.forEach((error) => {
        for (const context in error.context) {
          if (error.message && error.context?.hasOwnProperty(context)) {
            const regEx = new RegExp('{{' + context + '}}', 'g');
            const replacedValue = (<any>error.context)[context];
            error.message = error.message.replace(regEx, replacedValue);
          }
        }
      });
    }
  }

  hideError(index: number) {
    this.hideAlert.emit(index);
  }
}
