import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import {
  Component,
  OnInit,
  OnDestroy,
  Inject,
  Optional,
  ChangeDetectorRef,
} from '@angular/core';
import {
  FormGroup,
  AbstractControl,
  Validators,
  ValidatorFn,
} from '@angular/forms';
import { Observable, Subject, of, BehaviorSubject } from 'rxjs';
import {
  ArrangementList,
  PaymentFormField,
  PaymentFormFieldOptions,
  PaymentFormFieldConfig,
  InitiatorFields,
  PaymentFormFieldHooks,
  PaymentBaseFields,
  BB_CLOSED_PAYMENT_BUS_FN_SUFFIX,
  BB_INTRACOMPANY_PAYMENT_BUS_FN_SUFFIX,
  DefaultClosedPaymentBusFnSuffix,
  DefaultIntraCompanyPaymentBusFnSuffix,
  HiddenFields,
  InternalAccountFormItem,
  forbiddenStatus,
  CounterPartyFields,
  missingInListValidator,
  sameAccountsValidator,
} from '@backbase/payment-orders-ang';
import {
  getParentForm,
  isClosedPaymentValidator,
  registerFormControl,
} from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';
import {
  catchError,
  debounceTime,
  switchMap,
  takeUntil,
  tap,
} from 'rxjs/operators';
import { DeprecationsService } from '@backbase/foundation-ang/core';
import { SchemeNames } from '@backbase/data-ang/payment-order';
import { toggleBeneficiaryControls } from '../../helpers/generic-utils';

@Component({
  selector: 'bb-payord-debit-account-selector',
  templateUrl: 'payord-debit-account-selector.component.html',
})
export class PayordDebitAccountSelectorComponent
  implements PaymentFormField, OnInit, OnDestroy
{
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig;
  group!: FormGroup;

  control!: AbstractControl;
  /**
   * @deprecated
   * This property is deprecated from 3.10.3 and will be removed in next major release
   * Please use `arangementItems$` instead
   */
  items$!: Observable<Array<InternalAccountFormItem>>;
  arrangementItems$!: Observable<ArrangementList>;
  arrangementList!: ArrangementList;
  selectedAccount!: InternalAccountFormItem & { number?: string };
  initialDebitGroup = {
    [InitiatorFields.id]: [''],
    [InitiatorFields.name]: [''],
    [InitiatorFields.accountNumber]: [''],
    schemeName: [],
    currency: [''],
    balance: [''],
  };
  form!: FormGroup;
  gc$ = new Subject<void>();
  isClosedPaymentControl?: AbstractControl;
  private loadDebitAccountAndSelect$ = new Subject<undefined>();
  private accountIds: string[] = [];
  private currentPage = 0;
  private searchTerm = '';
  typeahead$ = new Subject<string>();
  loading$ = new BehaviorSubject(true);
  defaultTypeToSearchText = $localize`:@@debit-account-selector.default-type-to-search-text:No items found`;

  constructor(
    @Optional()
    @Inject(BB_CLOSED_PAYMENT_BUS_FN_SUFFIX)
    private closedPaymentBusFnSuffix: string,
    @Optional()
    @Inject(BB_INTRACOMPANY_PAYMENT_BUS_FN_SUFFIX)
    private intraCompanyPaymentBusFnSuffix: string,
    private readonly deprecationService: DeprecationsService,
    private cdRef: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.deprecationService.logDeprecatedFeature(
      `"BB_CLOSED_PAYMENT_BUS_FN_SUFFIX", "BB_INTRACOMPANY_PAYMENT_BUS_FN_SUFFIX", "DefaultClosedPaymentBusFnSuffix" and
      "DefaultIntraCompanyPaymentBusFnSuffix" are deprecated and will be removed in next major release`
    );
    this.closedPaymentBusFnSuffix =
      this.closedPaymentBusFnSuffix || DefaultClosedPaymentBusFnSuffix;
    this.intraCompanyPaymentBusFnSuffix =
      this.intraCompanyPaymentBusFnSuffix ||
      DefaultIntraCompanyPaymentBusFnSuffix;
    this.registerFields();
    this.listenSearch();
    this.control = this.group.controls['id'];
    this.form = getParentForm(this.control);
    this.isClosedPaymentControl = registerFormControl(
      this.form,
      HiddenFields.isClosedPayment
    );
    // populate value to show it in edit mode
    if (this.group.value.id) {
      this.selectItem(this.group.value);
    }
    // populate account balance and currency when applying a payment template or editing payment
    this.arrangementItems$ = this.options?.items$?.pipe(
      tap(({ items: list }: ArrangementList) => {
        this.accountIds = list.map((item: InternalAccountFormItem) => item.id);
        if (this.group.value.id) {
          const selectedAccount = list?.find(
            (item) => item.id === this.group.value.id
          );
          if (selectedAccount) {
            this.selectItem(selectedAccount);
          } else {
            // we need to load debit account to show it selected
            this.loadDebitAccountAndSelect$.next();
          }
        }
      }),
      catchError((err: HttpErrorResponse) => of(err))
    );

    this.arrangementItems$
      .pipe(takeUntil(this.gc$))
      .subscribe((arrangementList) => {
        this.loading$.next(false);
        this.arrangementList = arrangementList;
        this.cdRef.detectChanges();
      });

    const counterpartyGroup = <FormGroup>(
      this.form.get(PaymentBaseFields.counterparty)
    );

    if (this.options.checkClosedPaymentsAccess) {
      this.isClosedPaymentControl?.valueChanges
        .pipe(takeUntil(this.gc$))
        .subscribe((isClosedPayment: boolean) => {
          if (isClosedPayment) {
            toggleBeneficiaryControls(counterpartyGroup, isClosedPayment);
            this.control.updateValueAndValidity();
          }
        });

      this.options.arrangementPermissions$
        ?.pipe(takeUntil(this.gc$))
        .subscribe((response: HttpResponse<any>) => {
          this.isClosedPaymentControl?.setValue(
            response?.status === forbiddenStatus
          );
        });
    }
    this.loadDebitAccountAndSelect$
      .pipe(
        takeUntil(this.gc$),
        switchMap(
          () =>
            this.options.loadAccountByAccountId(
              this.group.value.id,
              this.group.value.accountNumber
            ) as Observable<InternalAccountFormItem | undefined>
        ),
        tap((item: InternalAccountFormItem | undefined) => {
          if (item) {
            this.accountIds.push(item.id);
          } else {
            this.control.setValidators([
              Validators.required,
              missingInListValidator(this.getAccountIdsList.bind(this)),
            ]);
            this.control.updateValueAndValidity();
          }
          const account = item || this.group.value;
          this.selectItem(account);
        }),
        catchError(() => of(undefined))
      )
      .subscribe();

    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  private fetchArrangementPermissions(arrangementId: string) {
    if (
      !this.options.isWizardMode &&
      this.options.checkClosedPaymentsAccess &&
      arrangementId
    ) {
      this.options.fetchArrangementPermissions$?.next(arrangementId);
    }
  }

  private getAccountIdsList() {
    return this.accountIds;
  }

  private listenSearch() {
    this.typeahead$
      .pipe(takeUntil(this.gc$), debounceTime(300))
      .subscribe((query: string) => {
        this.onSearch(query);
      });
  }

  ngOnDestroy() {
    this.gc$.next();
    this.gc$.complete();
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }

  registerFields() {
    const validators: { [key: string]: Array<ValidatorFn> } = {
      [InitiatorFields.id]: [Validators.required],
    };
    for (const key in this.initialDebitGroup) {
      if (this.initialDebitGroup.hasOwnProperty(key)) {
        if (key === InitiatorFields.id) {
          if (this.checkClosedPayment()) {
            validators[InitiatorFields.id].push(isClosedPaymentValidator);
          }
          if (this.isCounterpartyGroup()) {
            validators[InitiatorFields.id].push(sameAccountsValidator());
          }
        }
        registerFormControl(this.group, key, validators[key], [], '');
      }
    }
  }

  private isCounterpartyGroup(): boolean {
    return this.config.name === CounterPartyFields.counterPartyAccountGroup;
  }

  private checkClosedPayment(): boolean {
    return (
      !this.options.isWizardMode &&
      this.config.name === InitiatorFields.initiatorAccountGroup &&
      this.options.checkClosedPaymentsAccess
    );
  }

  selectItem(account: InternalAccountFormItem) {
    if (this.group && account.name) {
      this.group.patchValue({
        id: account.id,
        name: account.name,
        accountNumber: account.accountNumber,
        balance: account.balance,
        currency: account.currency,
        accountType: account.accountType,
        additions: account.additions || {},
        schemeName: SchemeNames.ID,
      });
      this.selectedAccount = {
        ...account,
        number: account.accountNumber,
        currency: account.currency,
        balance: account.balance,
      };

      if (!this.isCounterpartyGroup()) {
        this.form
          .get([PaymentBaseFields.counterparty, CounterPartyFields.id])
          ?.updateValueAndValidity();
      }

      this.fetchArrangementPermissions(account.id);
    }

    this.group.markAllAsTouched();
    this.group.markAsDirty();
  }

  onBlur() {
    if (this.options.required) {
      this.group.markAllAsTouched();
    }
  }

  onScrollToEnd(): void {
    this.options.fetchItems$.next({
      from: ++this.currentPage,
      searchTerm: this.searchTerm,
    });
    this.loading$.next(true);
  }

  onSearch(searchTerm: string) {
    this.currentPage = 0;
    this.searchTerm = searchTerm;
    this.options.fetchItems$.next({ from: this.currentPage, searchTerm });
    this.loading$.next(true);
  }
}
