import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import {
  PaymentFormFieldConfig,
  PaymentFormFieldHooks,
  PaymentFormFieldOptions,
  ScheduleFields,
} from '@backbase/payment-orders-ang';
import { Subject } from 'rxjs';
import { distinctUntilChanged, takeUntil } from 'rxjs/operators';
import { registerFormControl } from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';

@Component({
  selector: 'bb-payord-schedule-switch-ui',
  templateUrl: './payord-schedule-switch.component.html',
})
export class PayordScheduleSwitchComponent implements OnInit, OnDestroy {
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  private destroy$ = new Subject();
  readonly id = new Date().getTime();

  ngOnInit() {
    registerFormControl(
      this.group,
      this.config.name,
      this.options.validators,
      this.options.asyncValidators,
      this.options.defaultValue,
      this.config
    );
    registerFormControl(this.group, ScheduleFields.frequency);
    registerFormControl(this.group, ScheduleFields.scheduleInfo);

    triggerHook(PaymentFormFieldHooks.onInit, this);
    this.setValue();
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
    this.destroy$.next();
    this.destroy$.complete();
  }

  private setValue() {
    // listen changes to reset value before hiding this component
    this.group.controls[this.config.name]?.valueChanges
      .pipe(distinctUntilChanged(), takeUntil(this.destroy$))
      .subscribe((switchValue) => {
        // the condition is added to avoid unnecessary subscriptions when the component is hidden
        if (!this.config.hidden) {
          const freqValue = switchValue
            ? this.options.values?.on
            : this.options.values?.off;
          const infoValue = switchValue ? this.options.description : undefined;
          this.group.controls[ScheduleFields.frequency]?.patchValue(freqValue);
          this.group.controls[ScheduleFields.scheduleInfo]?.patchValue(
            infoValue
          );
        }
      });
  }
}
