import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';
import {
  pastDateValidator,
  requiredEndDateValidator,
  requiredEndTypeValidator,
  requiredAfterValidator,
} from '@backbase/payment-orders-ang';
import {
  PaymentFormField,
  PaymentFormFieldConfig,
  PaymentFormFieldOptions,
  ScheduleFields,
  PaymentFormFieldHooks,
  endTypes,
  ScheduleItem,
} from '@backbase/payment-orders-ang';
import { registerFormControl } from '../../helpers/form-helpers';
import { triggerHook } from '../../helpers/hooks-helpers';

@Component({
  selector: 'bb-payord-schedule',
  templateUrl: './payord-schedule.component.html',
})
export class PayordScheduleComponent
  implements OnInit, PaymentFormField, OnDestroy
{
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  scheduleGroup!: FormGroup;
  defaultMinOcccurs = 2;
  defaultMaxOcccurs = 100;

  ngOnInit() {
    this.registerFields(
      this.initScheduleFields(this.options.minOccurs, this.options.maxOccurs)
    );
    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }

  initScheduleFields(minOccurs: number, maxOccurs: number) {
    return {
      [ScheduleFields.frequency]: [
        { value: 'ONCE', every: '1' },
        [Validators.required],
      ],
      [ScheduleFields.startDate]: [
        new Date().toISOString(),
        [Validators.required, pastDateValidator],
      ],
      [ScheduleFields.endDate]: [
        new Date(
          new Date().getFullYear(),
          new Date().getMonth() + 1,
          new Date().getDate()
        ).toISOString(),
        [requiredEndDateValidator, pastDateValidator],
      ],
      [ScheduleFields.repeat]: [
        minOccurs || this.defaultMinOcccurs,
        [
          requiredAfterValidator,
          Validators.compose([
            Validators.min(minOccurs || this.defaultMinOcccurs),
            Validators.max(maxOccurs || this.defaultMaxOcccurs),
          ]),
        ],
      ],
      [ScheduleFields.endType]: [endTypes.never, [requiredEndTypeValidator]],
    };
  }

  registerFields(fields: { [key: string]: any[] }) {
    for (const key in fields) {
      if (fields.hasOwnProperty(key)) {
        registerFormControl(
          this.group,
          key,
          fields[key].pop(),
          [],
          fields[key].pop()
        );
      }
    }
  }

  get scheduleLabelData(): ScheduleItem {
    const { frequency, endDate, endType, repeat, startDate } = this.group.value;
    let end = {};
    switch (endType) {
      case endTypes.on: {
        end = { endDate };
        break;
      }
      case endTypes.after: {
        end = { repeat };
        break;
      }
    }
    return {
      frequency,
      endType,
      startDate,
      ...end,
    };
  }
}
