import { FormGroup } from '@angular/forms';
import { SanctionedCountry } from '@backbase/data-ang/payment-order-options';
import {
  CounterPartyFields,
  DependantItem,
  PurposeInfo,
} from '@backbase/payment-orders-ang';
import { isObjectTruthy } from '@backbase/payment-orders-ang';
declare const $localize: any;

export const mapLocalize = <T>(obj: T): T => {
  if (!isObjectTruthy(obj)) return obj;

  return Object.entries(obj).reduce((result, [key, value]) => {
    const localizedValue =
      typeof value === 'string' ? $localize`${value}` : value;
    return { ...result, [key]: localizedValue };
  }, {}) as T;
};

export function mapDependentItems(
  dependants: Array<string | DependantItem> = [],
  currentParent?: string
): DependantItem[] {
  const localDeps: string[] = [];
  const dependantItems = dependants.reduce((result, dep): DependantItem[] => {
    if (typeof dep === 'string') {
      localDeps.push(dep);
      return result;
    } else {
      return [...result, dep];
    }
  }, <DependantItem[]>[]);
  return [
    ...dependantItems,
    { parent: currentParent as string, items: localDeps },
  ];
}

export function getPurposeOfPaymentInfo(
  country?: SanctionedCountry
): PurposeInfo {
  const fieldName = 'purpose-of-payment';
  const result = { isOptional: true };
  const countryInfo = country?.inputFormSettings || [];
  const purposes = country?.purposesOfPayment;
  countryInfo.forEach((field) => {
    if (field.key === fieldName) {
      result.isOptional =
        field.mandatory !== undefined ? !field.mandatory : result.isOptional;
    }
  });

  return { ...result, purposes };
}

export function toggleBeneficiaryControls(
  formGroup: FormGroup,
  shouldDisableFields: boolean
): void {
  const controls = [
    CounterPartyFields.accountNumber,
    `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankBranchCode}`,
    `${CounterPartyFields.creditorBank}.${CounterPartyFields.bankName}`,
    `${CounterPartyFields.creditorBank}.${CounterPartyFields.bic}`,
  ];
  if (shouldDisableFields) {
    controls?.forEach((control) => formGroup.get(control)?.disable());
  } else {
    controls?.forEach((control) => formGroup.get(control)?.enable());
  }
}
